import { makeDecorator } from "../../metadata/index";

export const NG_ENTITY = 'NgEntity';

/**
 * 
 */
export interface NgEntity{
  /**
   * ,schemacode
   */
  originalCode: string;
  /**
   * ,schemalabel
   */
  nodeCode: string;
}
/**
 * 
 */
export interface NgEntityDecorator {
  (obj?: NgEntity): any;
  new(obj?: NgEntity): any;
}
/**
 * 
 * @param options options
 */
export function NgEntity(options: NgEntity){
  const decoratorFactory = makeDecorator(NG_ENTITY, (obj: NgEntity) => obj);
  return decoratorFactory(options);
}