import { format, isDate, parseISO, isEqual, compareAsc } from 'date-fns';

/**
 * 
 */
class DateUtil {

  /**
   * N
   * @tododevkit
   */
  // static emptyDateTimeString = '0001-01-01T00:00:00';
  static emptyDateTimeString = null;

  /**
   * ISO
   */
  // static emptyISODateTimeString = '0001-01-01T00:00:00+00:00';
  static emptyISODateTimeString = null;

  /**
   * 
   */
  static defaultISOFormat = `yyyy-MM-dd'T'HH:mm:ssxxx`;

  static defaultDisplayFormat = 'yyyy-MM-dd HH:mm:ss';

  static defaultDateFormat = 'yyyy-MM-dd';

  static defaultTimeFormat = 'HH:mm:ss';

  /**
   * ISO
   */
  static formatISO(dateOrDateString: string|Date): string {
    if (this.isEmptyDateOrDateString(dateOrDateString) === true) {
      return this.emptyISODateTimeString;
    }
    const dateObj = this.parse(dateOrDateString);
    return format(dateObj, this.defaultISOFormat);
  }

  /**
   * 
   * @param dateOrDateString ISO8601
   * @param dateFormat 
   */
  static format(dateOrDateString: string|Date, dateFormat?: string): string {
    if (this.isEmptyDateOrDateString(dateOrDateString) === true) {
      return this.emptyISODateTimeString;
    }
    const dateObj = this.parse(dateOrDateString);
    dateFormat = dateFormat ? dateFormat : this.defaultDisplayFormat;
    return format(dateObj, dateFormat);
  }

  /**
   * 
   * @param dateOrDateString ISO8601
   */
  static parse(dateOrDateString: string | Date): Date {
    if (this.isEmptyDateOrDateString(dateOrDateString) === true) {
      return null;
    }

    if (this.isDate(dateOrDateString) === true) {
      return dateOrDateString as Date;
    }

    return parseISO(dateOrDateString as string);
  }

  /**
   * 
   */
  static isDate(date: any): boolean {
    return isDate(date);
  }

  /**
   * 
   * @param dateOrDateString 
   */
  static isEmptyDateOrDateString(dateOrDateString: string | Date) {
    if (this.isDate(dateOrDateString) === true) {
      return this.isEmptyDate(dateOrDateString as Date);
    }
    return this.isEmptyDateString(dateOrDateString as string);
  }

  /**
   * 
   * @param date 
   */
  static isEmptyDate(date: Date) {
    if (!date) {
      return true;
    }
    return false;
  }

  /**
   * 
   * @param dateString 
   */
  static isEmptyDateString(dateString: string) {
    if (!dateString || dateString.startsWith('0001-01-01') === true) {
      return true;
    }
    return false;
  }

  /**
   * 
   * @param dateOrDateString1 
   * @param dateOrDateString1 
   * @return truefalse
   */
  static isEqual(dateOrDateString1: string|Date, dateOrDateString2: string|Date): boolean {
    const dateObj1 = this.parse(dateOrDateString1);
    const dateObj2 = this.parse(dateOrDateString2);
    if (dateObj1 === dateObj2) {
      return true;
    }
    return isEqual(dateObj1, dateObj2);
  }

  /**
   * 
   * @param dateOrDateString1 
   * @param dateOrDateString1 
   * @return -101
   */
  static compare(dateOrDateString1: string|Date, dateOrDateString2: string|Date) {
    const dateObj1 = this.parse(dateOrDateString1);
    const dateObj2 = this.parse(dateOrDateString2);
    if (this.isEqual(dateObj1, dateObj2) === true) {
      return 0;
    }

    // nullnull
    if (!dateObj1 && this.isDate(dateObj2) === true) {
      return -1;
    }
    if (!dateObj2 && this.isDate(dateObj1) === true) {
      return 1;
    }

    return compareAsc(dateObj1, dateObj2);
  }
}

export { DateUtil };

