import { Observable } from 'rxjs';

// tslint:disable-next-line: no-namespace
export namespace Expression {
  /**
   * 
   */
  export interface ExpressionObject {
    /**
     * 
     */
    id?: string;
    /**
     * 
     */
    path: string;
    /**
     * State|Entity
     */
    bindingType: ExpressionBindingType;
    /**
     * 
     */
    type: ExpressionType;
    /**
     * 
     */
    expression: string;
    /**
     * 
     */
    deps: Array<string>;
    /**
     * namespace
     */
    ns: string;
    /**
     * 
     */
    result?: any;
    /**
     * 
     */
    effectPaths?: string[],
    /**
     * 
     */
    message?: string;
    /**
     * 
     */
    messageType?: 'error' | 'info' | 'warning';
    /**
     * 
     */
    factory?: any;
  }

  /**
   *  
   */
  export interface ICustomContext {
    currentRows?: Array<ICurrentRow>,
    contexts?: { [prop: string]: any },
    patch?: { [prop: string]: any }
  }

  /**
   * 
   */
  export interface ICurrentRow {
    bindingPath: string;
    primaryValue: string;
  }

  /**
   * 
   * @deprecated deprecatednever use
   */
  export interface Assigner {
    assign(expressionObject: ExpressionObject);
  }
  /**
   * 
   */
  export interface IExecutor {
    compile(expressionObject: Expression.ExpressionObject, context?: { [prop: string]: any }): any;
  }
  /**
   * UIState?
   */
  export enum ExpressionBindingType {
    State = 'State',
    Field = 'Field'
  }
  /**
   * 
   */
  export enum ExpressionType {
    /**
     * 
     */
    Required = 'require',
    /**
     * 
     */
    Readonly = 'readonly',
    /**
     * 
     */
    Compute = 'compute',
    /**
     * 
     */
    Dependency = 'dependency',
    /**
     * 
     */
    Visible = 'visible',
    /**
     * 
     */
    Relative = 'relative',
    /**
     * 
     */
    Validate = 'validate',
    /**
     * 
     */
    DataPicking = 'dataPicking'
  }
  /**
   * 
   */
  export enum EventType {
    ValueChanged = 'VALUE_CHANGED',
    SelectionChanged = 'SELECTION_CHANGED',
    Load = 'Load',
    Append = 'Append',
    Remove = 'Remove',
    Update = 'Update',
  }
  export enum EventSource {
    Field = 'Field',
    State = 'State',
    BindingData = 'BindingData',
    Repository = 'Repository'
  }
  /**
   * 
   */
  export interface EventArgs {
    /**
     * namespace
     */
    ns: string;
    /**
     * 
     */
    path: Array<string>;
    /**
     * 
     */
    type?: EventType;
    /**
     * 
     */
    source: EventSource;
    /**
     * 
     */
    value?: any;
    /**
     * 
     */
    context?: any;
    /**
     * frameId
     */
    frameId?: string;
    /**
     * 
     */
    entity?: any;
    id?: any;
  }
  /**
   * 
   */
  export interface Listener {
    onEvent: Observable<EventArgs>;
  }
  /**
   * 
   */
  export interface Effector {
    /**
     * namespace
     */
    ns: string;
    /**
     * 
     * @param expressionObject 
     */
    effect(path: string, value: any, options: EffectOptions);
  }
  export interface EffectOptions {
    /**
     * 
     */
    path?: string[];
    /**
     * frameId
     */
    frameId?: string;
    /**
     * 
     */
    message?: string;
    /**
     * 
     */
    messageType?: MessageType | 'error' | 'info' | 'warning';
    /**
     * id
     */
    expressionId?: string;
  }
  export enum MessageType {
    error = 'error',
    info = 'info',
    warning = 'warning'
  }
  /**
   * 
   */
  export interface IEventHandler {
    handleEvent(event: EventArgs, expressionObjects: Expression.ExpressionObject[]);
    filter(event: Expression.EventArgs);
    perform(expressionObject: Expression.ExpressionObject, context: any);
    effect(event: Expression.EventArgs, expressionObject: Expression.ExpressionObject): void;
  }
  export enum EffectPath {
    currentRow = 0
  }
  /**
   * 
   */
  export interface IValidateResult {
    expressionId: string;
    path: string[];
    result: any;
  }
  /**
   * 
   */
  export interface IBuilderOptions {

  }
  export const MESSAGE = {
    'zh-CHS': {
      require: `'$property'`,
      validate: `'$property'`,
      dataPicking: ''
    },
    en: {
      require: `Please input '$property'`,
      validate: `'$property' calibration failed`,
      dataPicking: 'Failed to verify the expression before help'
    },
    'zh-CHT': {
      require: `'$property'`,
      validate: `'$property'`,
      dataPicking: ''
    }
  };
  export const DEPENDENCY_SPLITER = '/';
}