import { makePropDecorator } from '../metadata/index';

/**
 * NgComponentState
 */
export const NG_COMPONENT_STATE = 'NgParam';

/**
 * NgComponentState
 */
export interface NgParam {

  /**
   * ID
   */
  componentId?: string;

  /**
   * 
   */
  stateName?: string;
  /**
   * 
   */
  originalDataType?: string;
  /**
   * locale|remote
   */
  category?: string;

}

/**
 * NgUIStateDecorator
 */
export interface NgParamDecorator {
  (obj?: NgParam): any;
  new(obj?: NgParam): any;
}

/**
 * NgUIState
 */
export const NgParam: NgParamDecorator =
  makePropDecorator(NG_COMPONENT_STATE, (obj?: NgParam) => obj);


