import { Injectable, Injector, OnDestroy } from '@angular/core';
import { MetadataUtil } from '../metadata/index';
import { Command } from '../command/index';
import { NG_COMMAND, NgCommand, Keybinding } from './decorators';
import { BindingData, EntityValueChange } from '../binding-data/index';
import { UIState } from '../ui-state/index';
import { Form } from '../form/index';
import { StateMachine } from '../state-machine/index';
import { FrameContext } from '../frame/index';
import { Observable, of, Subject, from, EMPTY } from 'rxjs';
import { map, concatMap, tap, every, debounceTime, switchMap, takeLast } from 'rxjs/operators';
import { ExpressionManager, ExpressionResult } from '../expression/index';
import { IContextMetadata } from '../app/app_metadata';
import { InvokeOnValueChange } from '../binding-data/types';
import { IDisposable } from '../core/index';

/**
 * ViewModel
 *
 * ### ViewModel
 *
 * ViewModel
 *
 * 1ViewModelViewModel
 * 2NgViewModelSinmpleDemoBindingDataSimpleDemoForm
 *    SimpleDemoStateMachine
 * 3injectorViewModelinjectorNgViewModel
 *
 * ViewModel
 * ```ts
 * import { Injector, Injectable } from '@angular/core';
 * import { NgViewModel, ViewModel } from '@farris/devkit';
 *
 * @Injectable()
 * @NgViewModel({
 *   children: [],
 *   binding: SimpleDemoBindingData,
 *   form: SimpleDemoForm,
 *   stateMachine: SimpleDemoStateMachine,
 * })
 * class SimpleDemoViewModel extends ViewModel {
 *    constructor(injector: Injector) {
 *      super(injector);
 *    }
 *    @NgCommand({
 *      name: 'formLoad',
 *      params: {
 *        dataId: '1'
 *      }
 *    })
 *    public formLoad() {}
 * }
 * export { SimpleDemoViewModel };
 * ```
 *
 * ViewModel
 * ```ts
 * @Component({
 *   selector: 'app-simple-demo',
 *   templateUrl: './simple-demo.component.html'
 * })
 * class SimpleDemoComponent implements OnInit {
 *
 *   public viewModel: SimpleDemoViewModel;
 *
 *   constructor(viewModel: SimpleDemoViewModel) {
 *     this.viewModel = viewModel;
 *   }
 * }
 * ```
 *
 * ### ViewModel
 *
 * NgViewModel BindingDataFormStateMachine
 * ```html
 * * <!---->
 * <p>{{viewModel.bindingData.name}}</p>
 *
 * <!---->
 * <form [formGroup]="viewModel.form">
 *   <input type="text" formControlName="name">
 * </form>
 *
 * <!---->
 * <button type="button" [disabled]="!viewModel.stateMachine.canAdd"> </button>
 * * ```
 *
 * viewModelNgCommand
 * ```html
 * <button type="button" (click)="viewModel.add()"> </button>
 * ```
 *
 * ### ViewModle
 *
 * ViewModelViewModel
 * ViewModelNgViewModelchildrenViewModel
 * ViewModelViewModel
 * 
 * ```ts
 * @Injectable()
 *  @NgViewModel({
 *  children: [LeftListViewModel, RightCardViewModel],
 *    binding: NestedDemoBindingData,
 * })
 * class NestedDemoViewModel extends ViewModel {
 *   constructor(injector: Injector) {
 *     super(injector);
 *   }
 * }
 * export { NestedDemoViewModel };
 * ```
 */
@Injectable()
class ViewModel implements IDisposable, OnDestroy {

  public name: string;

  public frameContext: FrameContext;

  /**
   * 
   */
  public bindingData: BindingData;

  /**
   * 
   * /()/edus/edus/grades
   */
  public bindingPath: string;

  /**
   * 
   */
  public uiState: UIState;

  /**
   * 
   */
  public form: Form;

  /**
   * 
   */
  public stateMachine: StateMachine;

  /**
   * 
   */
  public verifyInformations: any[] = [];

  public verifycationChanged = new Subject<any[]>();
  /**
   * 
   */
  public get expression(): ExpressionManager {
    return this.frameContext.expressionManager;
  }
  /**
   * 
   */
  public get expressionResult(): ExpressionResult {
    return this.frameContext.expressionResult;
  }
  /**
   * 
   */
  public keybindingMap: Map<string, Keybinding>;

  /**
   * 
   */
  private entityValueChangingListeners: Map<string, string>;

  /**
   * 
   */
  private entityValueChangedListeners: Map<string, string>;
  /**
   * 
   */
  public metadatas: { [propName: string]: NgCommand };

  /**
   * kendogrid option
   */

  // constructor(metadata?: IContextMetadata) {
  //   if (!this.bindingPath && metadata && metadata.bindingTo) {
  //     this.bindingPath = metadata.bindingTo;
  //   }
  // }
  constructor() { }
  ngOnDestroy(): void {
    this.dispose();
  }

  dispose(options?: any) {
    // this.frameContext = null;
    // this.bindingData = null;
    // this.stateMachine = null;
    this.form = null;
    // this.uiState = null;

    if (this.entityValueChangingListeners) {
      this.entityValueChangingListeners.clear();
    }
    if (this.entityValueChangedListeners) {
      this.entityValueChangedListeners.clear();
    }
    if (this.verifycationChanged) {
      this.verifycationChanged.complete();
      this.verifycationChanged = null;
    }
    }

  public setMetadata(metadata: IContextMetadata) {
    if (!this.bindingPath && metadata && metadata.bindingTo) {
      this.bindingPath = metadata.bindingTo;
    }
  }
  /**
   * 
   */
  public init(context: FrameContext) {
    if (!this.name) {
      this.name = context.metadata.viewModelCode || this.constructor.name;
    }
    this.frameContext = context;
    this.bindingData = context.bindingData;
    this.uiState = context.uiState;
    this.form = context.form;
    this.stateMachine = context.stateMachine;
    this.buildCommands(context);
    this.entityValueChangingListeners = new Map<string, string>();
    this.entityValueChangedListeners = new Map<string, string>();
    // bindingData
    if (this.bindingData) {
      this.bindingData.setValueChangeInvokerFactory((paths: string[]): InvokeOnValueChange => {
        return (preValue, value, entityChanged: boolean, primaryValue?: any): Observable<boolean> => {
          const plainPath = '/' + paths.join('/');
          let command: string;
          if (entityChanged === false) {
            command = this.entityValueChangingListeners[plainPath];
          } else {
            command = this.entityValueChangedListeners[plainPath];
          }

          if (!!command) {
            const change: EntityValueChange = {
              paths: paths,
              preValue: preValue,
              value: value,
              id: primaryValue,
              changed: entityChanged
            };
            const commands = command.split(';').filter(p => p);
            let valueChangeSuccess = true;
            return from(commands).pipe(
              concatMap(item => {
                if (!valueChangeSuccess) {
                  return EMPTY;
                }
                return this[item](change).pipe(
                  tap((result: any) => {
                    valueChangeSuccess = result;
                  })
                );
              }),
              every((result: any) => result)
            );
            // return this[command](change).pipe(map(result => {
            //   return result === false ? false : true;
            // }));
          } else {
            return of(true);
          }
        };

      });
    }

    this.initListeners();
  }

  /**
   * 
   */
  public buildCommands(context: FrameContext) {
    const ngCommands: {
      [commandName: string]: NgCommand
    } = context.metadata.commands || MetadataUtil.getPropsMetadatasByName(this.constructor, NG_COMMAND);
    this.metadatas = ngCommands;
    this.keybindingMap = new Map<string, Keybinding>();
    Object.keys(ngCommands).forEach((propertyName: string) => {
      const ngCommand: NgCommand = ngCommands[propertyName];
      // 
      if (ngCommand.keyBinding) {
        this.keybindingMap.set(propertyName, ngCommand.keyBinding);
      }
      Object.defineProperty(this, propertyName, {
        value: (data: any) => {
          if (context.isDisposed) {
            return EMPTY;
          }
          // 
          let targetContext = context;
          if (ngCommand.frameId) {
            targetContext = context.appContext.getFrameContext(ngCommand.frameId);
          }
          const command: Command = {
            name: ngCommand.name,
            params: ngCommand.params,
            paramDescriptions: ngCommand.paramDescriptions,
            eventParam: data || null
          };
          return targetContext.commandBus.dispatch(command);
        }
      });
    });
  }

  /**
   * Form
   */
  private initListeners() {
    const extractPath = (bindingBasePath: string, bindingPath: string): string => {
      return '/' + bindingBasePath.split('/').concat(bindingPath.split('.')).filter((item) => item.length > 0).join('/');
    };

    if (this.form) {
      const valueChangingListeners = this.form.getEntityValueChangingListeners();
      Object.keys(valueChangingListeners).forEach((bindingPath) => {
        const plainPath = extractPath(this.bindingPath, bindingPath);
        this.entityValueChangingListeners[plainPath] = valueChangingListeners[bindingPath];
      });

      const valueChangedListeners = this.form.getEntityValueChangedListeners();
      Object.keys(valueChangedListeners).forEach((bindingPath) => {
        const plainPath = extractPath(this.bindingPath, bindingPath);
        this.entityValueChangedListeners[plainPath] = valueChangedListeners[bindingPath];
      });
    }
  }

  public bindToParent(parent: ViewModel) {
    if (parent) {
      if (parent.verifycationChanged) {
        parent.verifycationChanged.subscribe(verifyInformations => {
          if (this.verifycationChanged) {
            this.verifycationChanged.next(verifyInformations);
          }
        });
      }
    }
  }
  /**
   * 
   * @param expression 
   * @returns 
   */
  public transform(expression: string | boolean | Array<any>): any {
    if (Array.isArray(expression)) {
      const wfConf = expression.find(item => item && item.source === 'wf');
      if (wfConf && wfConf.value) {
        return this.transform(wfConf.value);
      } else {
        return this.transform(expression[0]);
      }
    } else {
      if (typeof expression === 'boolean') {
        return expression;
      } else if (typeof expression === 'string') {
        return new Function('ctx', `return ${expression}`).apply(this.frameContext, [this]);
      } else {
        // result
        return expression;
      }
    }
  }
}

export { ViewModel };
