import { Expression } from "../expression/index";

export class EffectorManager {
  /**
   * 
   * @param effector effector
   * @param expressionObject 
   * @param paths 
   * @returns 
   */
  public static effect(effector: Expression.Effector, expressionObject: Expression.ExpressionObject, paths: any[][]) {
    if (!paths || paths.length < 1) {
      return;
    }
    paths.forEach((path: any[]) => {
      const effectOptions: Expression.EffectOptions = { path, message: expressionObject.message, expressionId: expressionObject.id };
      effector.effect(expressionObject.path, expressionObject.result, effectOptions);
    });
  }
}