import { Observable, Subject } from "rxjs";
import { Change } from "../binding-data/index";
import { Modification } from "../changeset/index";
import { DataPropGroup, DataPropInfo, DataTypeInfo } from "../core";
import { Expression } from '../expression/index';
import { UIStateObservableParam } from "../ui-state/index";

type EventArgs = Expression.EventArgs;
type Listener = Expression.Listener;

/**
 * 
 */
export abstract class ChangeListener implements Listener {
  protected subject: Subject<EventArgs> = new Subject<EventArgs>();
  public get onEvent(): Observable<EventArgs> {
    return this.subject;
  }
  abstract buildEventPath(change: Change | Modification |UIStateObservableParam): string[];
  /**
   * 
   * @param dataTypeInfo 
   * @param results 
   * @param paths 
   */
  public findEntityPaths(dataTypeInfo: DataTypeInfo, results: any[], paths: string[] = []) {
    const list: DataPropInfo[] = dataTypeInfo.getPropInfosByGroup(DataPropGroup.List);
    if (list && list.length > 0) {
      list.forEach((dataPropInfo: DataPropInfo) => {
        paths.push(dataPropInfo.name);
        const childrens: DataPropInfo[] = dataPropInfo.typeInfo.getPropInfosByGroup(DataPropGroup.List);
        if (childrens && childrens.length > 0) {
          childrens.forEach((dataPropInfo: DataPropInfo) => {
            this.findEntityPaths(dataPropInfo.typeInfo, results, paths);
          });
        } else {
          results.push(paths);
        }
      });
    } else if (paths && paths.length > 0) {
      results.push(paths);
    }
  }
}