import { Injectable, Inject, Optional } from '@angular/core';
import { Repository } from '../repository/index';
import { Entity } from '../entity/index';
import { IDisposable } from '../core';

/**
 * Repository
 */
@Injectable()
class RepositoryManager implements IDisposable {
  /**
   * repositoryMap
   */
  private repositoryMap: Map<string, Repository<Entity>>;

  /**
   * 
   */
  constructor() {
    this.repositoryMap = new Map<string, Repository<Entity>>();
  }

  /**
   * Repository
   */
  public regRepository(name: string, repository: Repository<Entity>): void {
    this.repositoryMap.set(name, repository);
  }

  public unRegisteRepository(name: string) {
    this.repositoryMap.delete(name);
  }
  /**
   * RepositoryMap
   * @internal
   */
  public getRepositoryMap(): Map<string, Repository<Entity>> {
    return this.repositoryMap;
  }

  /**
   * Repository
   */
  public getRepositories(): Repository<Entity>[] {
    return Array.from(this.repositoryMap.values());
  }

  /**
   * nameRepository
   */
  public getRepositoryByName(name: string): Repository<Entity> {
    return this.repositoryMap.get(name);
  }

  /**
   * Repository
   */
  public ifRepositoryExits(name: string) {
    const repository = this.getRepositoryByName(name);
    return repository ? true : false;
  }
  public dispose() {
    // this.repositoryMap.forEach((repo: Repository<any>) => {
    //   if (repo) {
    //     repo.entityCollection.reset(false);
    //   }
    // });
    this.repositoryMap.clear();
  }

}

export { RepositoryManager };
