// tslint:disable: max-line-length
import { UID } from './uid';

/**
 * GUID
 * @scope Provider
 */
export class Guid {

    private constructor(guid: string) {
        if (!guid) { throw new TypeError('Invalid argument; `value` has no value.'); }

        this.value = Guid.EMPTY;

        if (guid) {
            this.value = guid;
        }
    }

    public static validator = new RegExp('^[a-z0-9]+$', 'i');

    public static EMPTY = '';

    private value: string;

    public static isGuid(guid: any) {
        const value: string = guid.toString();
        return guid && (guid instanceof Guid || Guid.validator.test(value));
    }

    public static create(): Guid {
        return new Guid(UID.create());
    }

    public static createEmpty(): Guid {
        return new Guid('');
    }

    public static parse(guid: string): Guid {
        return new Guid(guid);
    }

    public static raw(): string {
        return UID.create();
    }

    public equals(other: Guid): boolean {
        // Comparing string `value` against provided `guid` will auto-call
        // toString on `guid` for comparison
        return Guid.isGuid(other) && this.value === other.toString();
    }

    public isEmpty(): boolean {
        return this.value === Guid.EMPTY;
    }

    public toString(): string {
        return this.value;
    }

    public toJSON(): any {
        return {
            value: this.value,
        };
    }
}
