import { Type, Injectable, OnDestroy } from '@angular/core';
import { Observable, Subject } from 'rxjs';

import { DataTypeInfo, IDisposable } from '../core/index';
import { Modification } from '../changeset/index';
import { MetadataUtil } from '../metadata/index';
import { Entity, createEntity, createEntities, FieldMetadataUtil } from '../entity/index';
import { NgRepository } from './decorators';
import { EntityCollection } from './entity_collection';
import { PaginationManager } from './pagination_manager';
import { DataChangeHistory } from './data_change_history';
import { SortConditionManager } from './sort_condition_manager';
import { FilterConditionManager } from './filter_condition_manager';
import { UID } from '../misc/uid';

/**
 * 
 * @description
 * # Repository
 * 
 * RepositoryEntityRepository
 * RepositoryEntityAPIRepositoryBindingDataBindingData
 * 
 * ## 
 * Repository
 * 
 * - 
 * 
 *   ```javascript
 *    this.repository.entityCollection.getEntityById("id");
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.entityCollection.removeEntityById("id")
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.entityCollection.clear();
 *   ```
 * 
 * - json
 * 
 *   ```javascript
 *   this.repository.entityCollection.toJSON();
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.entityCollection.addEntity(entity:T);
 *   ```
 * - 
 * 
 *   ```javascript
 *   const befRepository = this.repository as BefRepository<any>;
 *   const entityManager = befRepository.entityManager;
 *   entityManager.appendEntityByPath("/Id/s",{JSON});
 *   ```
 * - 
 * 
 *   ```javascript
 *   this.repository.entityCollection.addEntities(entities:T[])
 *   ```
 *   
 * - 
 * 
 *   ```javascript
 *   const befRepository = this.repository as BefRepository<any>;
 *   const entityManager = befRepository.entityManager;
 *   const childrenEntities = entityManager.createEntitiesByPath(["Id","s"], entityListData: any[])
 *   entityManager.appendEntitiesByPath(["Id","s"],childrenEntities);
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.entityCollection.loadEntities(entities:T[]);
 *   ```
 * 
 *   > 
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.buildEntity(JSON);
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.buildEntities(JSON);
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   const befRepository = this.repository as BefRepository<any>;
 *   const entityManager = befRepository.entityManager;
 *   entityManager.getEntityByPath(["Id","","id"]);
 *   ```
 * 
 *   !> **** entityManagerBefRepositoryEntityManagerRepositoryBefRepository
 * 
 * - 
 * 
 *   ```javascript
 *   const befRepository = this.repository as BefRepository<any>;
 *   const entityManager = befRepository.entityManager;
 *   entityManager.getEntitiesByPath(["Id",""]);
 *   ```
 * 
 *   > EntityManager
 *   >
 *   > ```javascript
 *   > entityManager.getEntitiesByPath(["Id"]);
 *   > ```
 *   
 * - 
 * 
 *   ```javascript
 *   const entity = this.repository.entityCollection.getEntityById("id");
 *   cosnt name = entity['name'];
 *   // 
 *   const entity: any = this.repository.entityCollection.getEntityById("id");
 *   cosnt name = entity.name;
 *   ```
 * 
 * ## 
 * 
 * 	
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.updateById('id');
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.removeById("id");
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.removeByIds(ids:string[])
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.removeByPath(['Id','','Id'])
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.append();
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.appendByPath(['id','']);
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.getById('id');
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.getEntities(filter: any[], sorts: any[], pageSize: number | null, pageIndex: number | null)
 *   
 *   ```
 * 
 *   > pageSizepageIndex
 * 
 * - POST
 * 
 *   ```javascript
 *   this.repository.filter(filter: any[], sorts: any[], pageSize: number | null, pageIndex: number | null)
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.updateChangesById("id");
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.updateAllChanges();
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.applyChanges();
 *   ```
 * 
 * - 
 * 
 *   ```javascript
 *   this.repository.cancelChanges();
 *   ```
 * 
 * - Repository
 * 
 *   ```javascript
 *   const befRepository = this.repository as BefRepository<any>;
 *   befRepository.entityManager.clearAllEntityChanges();
 *   ```
 *   
 * - 
 *   ```javascript
 *   this.repository.batchAppendByPath(path: string, defaultValues: Array<any>);
 *   ```
 *   
 *   > path `string`/id/nodeCode/1/orders
 *   >
 *   > defaultValues `Array<any>`keyvalue
 *   
 * - 
 *   
 *   ```javascript
 *   this.repository.batchRemoveByPath(path: string, ids: string);
 *   ```
 *   
 *   > path `string` /id/nodeCode/1/orders
 *   >
 *   > ids `string` id`id1,id2,id3`
 */
@Injectable()
export abstract class Repository<T extends Entity> implements IDisposable, OnDestroy {

  // #region 

  /**
   * 
   */
  private innerName: string;

  // #endregion

  // #region 

  /**
   * API
   */
  public apiUri: string;

  /**
   * 
   */
  public entityType: any;


  /**
   * 
   * @todoEntityTypeInfoDataTypeInfo
   */
  public entityTypeInfo: DataTypeInfo;

  /**
   * 
   */
  public entityCollection: EntityCollection<T>;

  /**
   * 
   */
  public paginationInfo: any = null;

  /**
   * 
   */
  public dataChangeHistory: DataChangeHistory;

  /**
   * 
   */
  public sortConditionManager: SortConditionManager;
  /**
   * 
   */
  public filterConditionManager: FilterConditionManager;
  // #endregion

  // #region get/set

  /**
   * 
   */
  public get primaryKey(): string {
    return this.entityCollection.primaryKey;
  }
  public get changes(): Subject<Modification> {
    return this.entityCollection.changes;
  }
  /**
   * 
   */
  public get entityCollectionChange(): Subject<Modification> {
    return this.entityCollection.collectionChanged;
  }

  /**
   * 
   */
  public get name(): string {

    // 1name
    // 2construcotr.namename
    if (!this.innerName) {
      const guid = UID.create();
      this.innerName = `Repository_${guid}`;
    }

    return this.innerName;
  }
  /**
   * 
   */
  public set name(value: string) {
    this.innerName = value;
  }

  public paginationManager: PaginationManager<T>;
  public destroy$: Subject<any>;
  // #endregion

  /**
   * 
   */
  constructor() {
    this.readMetadata();
    if (this.entityType) {
      this.entityTypeInfo = new DataTypeInfo(this.entityType);
      this.entityCollection = new EntityCollection<T>(this.entityType);
    }
    this.dataChangeHistory = new DataChangeHistory();
    this.sortConditionManager = new SortConditionManager();
    this.filterConditionManager = new FilterConditionManager();
    this.destroy$ = new Subject<any>();
  }
  dispose(options?: any) {

    this.paginationManager = null;
    if (this.destroy$) {
      this.destroy$.next();
      this.destroy$.complete();
      this.destroy$ = null;
    }
    if (this.entityCollection) {
      this.entityCollection.reset(false);
      this.entityCollection = null;
    }

  }
  ngOnDestroy(): void {
    this.dispose();
  }

  public updateEntityType(entityType: any) {
    this.entityType = entityType;
    this.entityTypeInfo = new DataTypeInfo(this.entityType);
    this.entityCollection = new EntityCollection<T>(this.entityType);
  }


  /**
   * 
   */
  private readMetadata() {
    const repositoryMetadata: NgRepository = MetadataUtil.getClassMetadataByName(this.constructor, 'NgRepository');
    if (repositoryMetadata) {
      // API
      this.apiUri = repositoryMetadata.apiUrl;

      // 
      this.entityType = repositoryMetadata.entityType;
    }

    // // API
    // this.apiUri = repositoryMetadata.apiUrl;

    // // 
    // this.entityType = repositoryMetadata.entityType;
  }

  /**
   * 
   * @param config 
   */
  setPaginationConfig(config: any) {
    this.paginationManager = new PaginationManager(this.entityType, config);
    const { pageSize = 0 } = this.paginationManager.getPaginationConfigByPath('/') || {};
    // tslint:disable-next-line: max-line-length
    this.entityCollection.paginationInfo = Object.assign({ pageSize }, this.paginationManager.pagination, this.entityCollection.paginationInfo);
    // pageSize
    // this.entityCollection.pageSize = pageSize;
  }
  /**
   * 
   */
  public reset(): void {
    this.entityCollection.reset();
  }

  /**
   * 
   */
  public buildEntity(data: any): T {
    const entity = createEntity<T>(this.entityType, data);
    return entity;
  }

  /**
   * 
   */
  public buildEntities(listData: any[]): T[] {
    const entities: T[] = createEntities<T>(this.entityType, listData);
    return entities;
  }

  /**
   * 
   * @param filters    
   * @param sorts      
   * @param pagination 
   */
  abstract getEntities(filter: any[], sorts: any[], pageSize: number | null, pageIndex: number | null): Observable<T[]>;
  /**
   * 
   * @param filter 
   * @param sorts 
   * @param pageSize 
   * @param pageIndex 
   */
  abstract filter(filter?: any[], sorts?: any[], pageSize?: number | null, pageIndex?: number | null): Observable<T[]>;
  /**
   * 
   */
  abstract getList(): Observable<T[]>;
  /**
   * id
   * @param id 
   */
  abstract getById(id: string): Observable<T>;
  /**
   * id
   * @param id id
   */
  abstract getEntityById(id: string): Observable<T>;
  /**
   * /
   * @param path 
   * @param ids 
   * @param filter 
   * @param sort 
   */
  abstract queryChild(paths: string[], pageIndex: number, pageSize: number, filter?: string, sort?: string): Observable<T[]>;
  /**
   * id
   * @param id id
   */
  abstract updateById(id: string): Observable<T>;
  /**
   * ()
   * @param id 
   */
  abstract updateEntityById(id: string): Observable<T>;
  /**
   * 
   */
  abstract create(): Observable<any>;

  // /**
  //  * 
  //  * @param path 
  //  */
  // abstract createByPath(path: string);

  /**
   * 
   */
  abstract append(): Observable<any>;

  /**
   * 
   * @param path 
   */
  abstract appendByPath(path: string): Observable<any>;
  /**
   * 
   */
  abstract insert(position: 1 | -1, defaultValue?: any): Observable<any>;
  /**
   * 
   * @param path 
   */
  abstract insertByPath(path: string, position: 1 | -1): Observable<any>;
  /**
   * id
   * @param id 
   */
  abstract removeById(id: string, ifSave?: boolean): Observable<any>;

  /**
   * ids
   * @param id ids
   * @param ifSave 
   * @deprecated querystringbatchRemove
   */
  abstract removeByIds(ids: string[], ifSave?: boolean): Observable<any>;
  /**
   * ids
   * @param ids ids
   * @param ifSave 
   */
  abstract batchRemove(ids: string[], ifSave?: boolean): Observable<any>;
  /**
   * idpath
   */
  abstract removeByPath(path: string, id: string);

  /**
   * id
   */
  abstract updateChangesById(id: string): Observable<boolean>;

  /**
   * path
   */
  abstract updateChangesByPath(path: string, id: string): Observable<boolean>;

  /**
   * 
   */
  abstract updateAllChanges(): Observable<boolean>;

  /**
   * 
   */
  abstract applyChanges(): Observable<boolean>;

  /**
   * id
   */
  abstract applyChangesById(id: string): Observable<boolean>;

  /**
   * 
   */
  abstract cancelChanges(options?: any): Observable<boolean>;
  /**
   * 
   */
  abstract batchRemoveByPath(path: string, ids: string): Observable<boolean>;
  /**
   * /
   * @param path like 1/childs or 1/childs/11/childchilds
   * @param defaultValues 
   * @description 
   */
  abstract batchAppendByPath(path: string, defaultValues: Array<any>): Observable<any>;
  /**
   * 
   * @param defaultValues 
   */
  abstract batchAppend(defaultValues: Array<any>): Observable<any>;

}
