/**
 * 
 */
export enum BindingPropertyType {

  /**
   * 
   */
  Plain = 'Plain',

  /**
   * 
   */
  Object = 'Object',

  /**
   * 
   */
  List = 'List',

  /**
   * 
   */
  Dynamic = 'Dynamic'
}


/**
 * 
 */
export interface BindingProperty {

  /**
   * 
   */
  name: string;

  /**
   * 
   */
  type: BindingPropertyType;

  /**
   * ObjectList
   */
  entityType?: any;

  /**
   * 
   */
  isPrimaryKey?: boolean;

  /**
   * 
   */
  enableMultiLangInput?: boolean;
}
