import { Expression } from '../expression/index';
import { Injectable, Injector } from '@angular/core';
import { EffectorRegistry } from './effector_registry';
import { RepositoryEffector } from './repository_effector';
import { UIStateEffector } from './uistate_effector';
import { ReadonlyEffector } from './readonly_effector';
import { DependencyEffector } from './dependency_effector';
import { RelativeEffector } from './relative_effector';
import { ValidateEffector } from './validate_effector';
import { RequiredEffector } from './required_effector';
import { VisibleEffector } from './visible_effector';
// import { Core } from '../core/index';

@Injectable()
export class EffectorFactory {
  constructor(private injector: Injector, private effectorRegistry: EffectorRegistry) { }
  public getEffector(expressionObject: Expression.ExpressionObject) {
    const path = expressionObject.path;
    const ns = expressionObject.ns;
    const bindingType = expressionObject.bindingType;
    const type = expressionObject.type;
    const nsEffectors = this.effectorRegistry.effectors.filter((effector: Expression.Effector) => effector.ns == ns);
    // 
    if (type === Expression.ExpressionType.Compute) {
      if (bindingType === Expression.ExpressionBindingType.Field) {
        return nsEffectors.find((effector: Expression.Effector) => effector instanceof RepositoryEffector);
      } else if (bindingType === Expression.ExpressionBindingType.State) {
        return nsEffectors.find((effector: Expression.Effector) => effector instanceof UIStateEffector);
      } else {
        throw new Error(`${bindingType}`);
      }
    } else if (type === Expression.ExpressionType.Readonly) {
      // 
      return nsEffectors.find((effector: Expression.Effector) => effector instanceof ReadonlyEffector);
    } else if (type === Expression.ExpressionType.Dependency) {
      // 
      return nsEffectors.find((effector: Expression.Effector) => effector instanceof DependencyEffector);
    } else if (type === Expression.ExpressionType.Relative) {
      // 
      return nsEffectors.find((effector: Expression.Effector) => effector instanceof RelativeEffector);
    } else if (type === Expression.ExpressionType.Validate) {
      // 
      return nsEffectors.find((effector: Expression.Effector) => effector instanceof ValidateEffector);
    } else if (type === Expression.ExpressionType.Required) {
      // 
      return nsEffectors.find((effector: Expression.Effector) => effector instanceof RequiredEffector);
    } else if (type === Expression.ExpressionType.Visible) {
      // 
      return nsEffectors.find((effector: Expression.Effector) => effector instanceof VisibleEffector);
    } else {
      // Core.warn(`EffectorFactory  ${type}`);
      return null;
    }
  }
}
