import { BindingData, BindingPropertyType, PropertyUtil } from '../binding-data/index';
/**
 * /PathNode1/PathNode2/...
 */
class DataPathUtil {

  /**
   * BindingData
   */
  public static convertToBindingPathArray(path: string): string[] {
    const bindingPathArray = path.split('/').filter((part: string) => {
      return part !== '';
    });
    return bindingPathArray;
  }

  /**
   * Entity
   * []
   * ['id:xxx', 'name'],
   * ['id:xxx', 'deptInfo', 'id:xxx', 'name']
   * UDT: ['id:xxx', 'updateInfo', ':', 'createdOn']
   * ['id:xxx', 'edus', 'id:xxx', 'name'],
   * ['id:xxx', 'edus', 'id:xxx', 'grades', 'id:xxx', 'name']
   */
  public static convertToEntityPathArray(path: string, bindingData: BindingData): string[] {
    const bindingPathArray: string[] = this.convertToBindingPathArray(path);
    const entityPathArray: string[] = [];

    if (bindingPathArray.length === 0) {
      return entityPathArray;
    }

    // 
    let currentBindingObject = bindingData.list.currentItem;
    entityPathArray.push(
      this.createPrimaryKeyPath(currentBindingObject.primaryKey, currentBindingObject.primaryKeyValue)
    );

    bindingPathArray.forEach((propName: string) => {
      const propInfo = PropertyUtil.getPropertyByName(currentBindingObject.properties, propName);
      switch (propInfo.type) {
        case BindingPropertyType.Plain:
          entityPathArray.push(propName);
          break;
        case BindingPropertyType.Object:
          currentBindingObject = currentBindingObject[propName];
          entityPathArray.push(propName);
          entityPathArray.push(
            this.createPrimaryKeyPath(currentBindingObject.primaryKey, currentBindingObject.primaryKeyValue)
          );
          break;
        case BindingPropertyType.List:
          const currentBindingList = currentBindingObject[propName];
          currentBindingObject = currentBindingList.currentItem;
          entityPathArray.push(propName);
          entityPathArray.push(
            this.createPrimaryKeyPath(currentBindingObject.primaryKey, currentBindingObject.primaryKeyValue)
          );
          break;
        default:
            break;
      }
    });

    return entityPathArray;
  }

  /**
   * RestUrl
   *
   * 
   * //
   * /jiwtEdus/xxx/jiwtEdus
   * /jiwtEdus/jiwtGrades /xxx/jiwtEdus/xxx/jiwtGrades
   */
  public static convertToRestUrl(path: string, bindingData: BindingData): string {
    const bindingPathArray: string[] = this.convertToBindingPathArray(path);
    const restPathArray: string[] = [];

    let currentBindingObject = bindingData.list.currentItem;
    restPathArray.push(currentBindingObject.primaryKeyValue);

    bindingPathArray.forEach((propName: string) => {
      const propInfo = PropertyUtil.getPropertyByName(currentBindingObject.properties, propName);
      if (propInfo.type !== BindingPropertyType.List) {
        throw new Error(`${propInfo.name}`);
      }
      const currentBindingList = currentBindingObject[propName];
      currentBindingObject = currentBindingList.currentItem;
      restPathArray.push(propName);
      restPathArray.push(currentBindingObject.primaryKeyValue);
    });

    // 
    restPathArray.pop();
    return '/' + restPathArray.join('/');
  }

  /**
   * Path
   */
  public static getLeafPath(path: string): string {
    const pathArray = DataPathUtil.convertToBindingPathArray(path);
    return pathArray.pop();
  }

  /**
   * 
   */
  public static getParentPath(path: string): string {
    const pathArray = DataPathUtil.convertToBindingPathArray(path);
    pathArray.pop();
    return '/' + pathArray.join('/');
  }

  /**
   * 
   */
  private static createPrimaryKeyPath(primaryKey: string, primaryKeyValue: string) {
    return `${primaryKey}:${primaryKeyValue}`;
  }
}

export { DataPathUtil };
