import { Inject, Injectable, Injector, Optional } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { ListenerRegistry } from './listener_registry';
import { Expression } from '../expression/index';

type EventArgs = Expression.EventArgs;
type Listener = Expression.Listener;

/**
 * 
 * @description bindingListUIState
 */
@Injectable()
class Listeners {
  private subject: Subject<EventArgs> = new Subject<EventArgs>();
  public get onEvent(): Observable<EventArgs> {
    return this.subject;
  }
  constructor(@Optional() private registry: ListenerRegistry) {
    this.regist();
  }
  private regist() {
    const listeners = this.registry && this.registry.listeners || [];
    if (listeners && listeners.length > 0) {
      listeners.forEach((listener: Listener) => {
        listener.onEvent.subscribe((modification: EventArgs) => {
          this.subject.next(modification);
        });
      });
    }
  }
}
export { Listeners };
