import { Inject, Injectable, Injector } from '@angular/core';
import { BindingData } from '../binding-data/index';
import { Entity, EntityList } from '../entity/index';
import { Expression } from '../expression/index';
import { NAMESPACE } from '../frame/tokens';
import { Repository } from '../repository/index';

/**
 * 
 * @description true
 * value==truenullnull
 */
@Injectable()
export class DependencyEffector implements Expression.Effector {
  public ns: string;
  constructor(private injector: Injector, private repository: Repository<Entity>, @Inject(NAMESPACE) private namespace, private bindingData: BindingData) {
    this.ns = namespace;
  }
  public effect(path: string, value: boolean, options: Expression.EffectOptions) {
    if (!options || !options.path) {
      throw new Error('DependencyEffector ');
    }
    if (typeof value !== 'boolean') {
      console.warn(`DependencyEffector true/false${value}`);
    }
    const propertyPath = options.path;
    const rowId = propertyPath[0] || this.bindingData.list.currentItem.primaryKeyValue;
    let entity = this.repository.entityCollection.getEntityById(rowId);
    if (rowId && !entity) {
      throw new Error(`id${rowId}`);
    }
    // propertyPath like : [1, child1s, 1.1, child2s, 1.1.1,propName] or [1,udt,propName] or [1,prop]
    const propName = propertyPath.pop();
    let object: any = entity;
    for (let index = 1; index < propertyPath.length; index++) {
      const propertyName = propertyPath[index];
      if (object instanceof EntityList) {
        object = object.get(propertyName);
      } else {
        object = object[propertyName];
      }
    }
    if (object) {
      if (object[propName] !== null && value === true) {
        object[propName] = null;
      }
    } else {
      throw new Error(`[DependencyEffector] ${propertyPath.push(propName)}`);
    }
  }

}