import { InjectionToken } from '@angular/core';
import { AppContext } from '../app/index';

// export type ClassType = new (...args: any[]) => any;
export const BigNumberType = 'BigNumber';
export interface Pagination {
  pageSize: number;
  pageIndex: number;
  total: number;
}
export interface ResultData {
  data?: any[];
  pagination?: Pagination;
}

export enum DataChangeType { Add, Delete }

export interface DataChange {
  /**
   * 
   */
  fpath?: string;
  dataId: string;
  changeType: DataChangeType;
  [prop: string]: any;
}
export enum HttpMethod {
  GET = 'GET',
  POST = 'POST',
  PUT = 'PUT',
  DELETE = 'DELETE'
}

/**
 * 
 */
// tslint:disable-next-line: no-namespace
export namespace BackEndMessage {
  /**
   * 
   */
  export enum Level {
    Error = 'Error',
    Info = 'Info',
    Warning = 'Warning'
  }
  /**
   * 
   */
  export interface IMessage {
    context: IMessageContext;
    bizMessages: IBizMessage[];
  }
  /**
   * 
   */
  export interface ILocation {
    /**
     * 
     */
    nodeCode: string;
    /**
     * 
     */
    rows?: string[];
    /**
     * 
     */
    columns?: string[];
  }
  /**
   * 
   */
  export interface IBizMessage {
    /**
     * 
     */
    location?: ILocation;
    /**
     * 
     */
    message: string;
    /**
     * 
     */
    level: Level;
  }
  /**
   * 
   */
  export interface IMessageContext {
    ns: string;
    appContext: AppContext;
  }
  /**
   * 
   * @description info/warning
   */
  export class Message implements IMessage {
    public context: IMessageContext;
    public bizMessages: IBizMessage[];
    constructor(bizMessages: IBizMessage[], context: IMessageContext) {
      this.bizMessages = bizMessages;
      this.context = context;
    }
  }
  /**
   * 
   */
  export interface IBackEndMessageHandler {
    handle(message: IMessage, context?: any);
  }
}

export interface IMessageService {
  /**
   * 
   * @param message 
   */
  info(message: string);
  /**
   * 
   * @param message 
   */
  error(message: string);
  /**
   * 
   * @param message 
   */
  warning(message: string);
}

export interface INotifyService {
  /**
   * 
   * @param message 
   * @param options 
   */
  info(message: string, options?: { [prop: string]: any });
  /**
   * 
   * @param content 
   * @param options 
   */
  success(content: string, options?: { [prop: string]: any });
  /**
   * 
   * @param content 
   */
  warning(content: string);
  /**
   * 
   * @param content 
   * @param options 
   */
  error(content: string, options?: { [prop: string]: any });
}

export enum RunMode {
  compatible = 'compatible',
  highSpeed = 'highSpeed'
}
/**
 * compatible | highSpeed
 */
export const DEVKIT_RUN_MODE = new InjectionToken<RunMode>('@farris/devkit_run_mode');
export interface IDisposable {
  dispose(options?: any);
}