import { Inject, Injectable, Injector } from '@angular/core';
import { Expression } from '../expression/index';
import { FrameContext, NAMESPACE } from '../frame/index';
import { Repository } from '../repository/index';
import { ExpressionUtil } from '../utils/expression_util';

@Injectable()
export class VisibleEffector implements Expression.Effector {
  public ns: string;
  constructor(
    private injector: Injector,
    @Inject(NAMESPACE) private namespace,
    private frameContext: FrameContext,
    private repository: Repository<any>) {
    this.ns = namespace;
  }
  public effect(path: string, value: any, options: Expression.EffectOptions) {
    // ignore
    const paths = path.split('/').filter(p => p);
    const bindingPaths = this.getTablePaths(paths);
    const bindingPath = bindingPaths.join('/');
    // 
    if (bindingPaths && bindingPaths.length > 0) {
      const isGridComponent = this.isGridComponent(bindingPath);
      if (isGridComponent) {
        const datagridComponent = this.getDatagridComponent(bindingPath);
        if (datagridComponent) {
          // 
          // datagridComponent.columnsChanged();
          const fieldPaths = this.getPropertyPaths(paths);
          if (fieldPaths) {
            const field = fieldPaths.join('.');
            if (value) {
              datagridComponent.showColumn(field);
            } else {
              datagridComponent.hideColumn(field);
            }
          }
        }
      }
    } else {
      const datagridComponent = this.getDatagridComponent(bindingPath);
      if (datagridComponent) {
        datagridComponent.columnsChanged(false);
      }
    }
  }
  private getTablePaths(paths: string[]): string[] {
    const entityPaths = ExpressionUtil.getAvailableChildrenPathsFromEntityPaths(paths, this.repository.entityTypeInfo);
    return entityPaths;
  }
  private getDatagridComponent(bindingPath: string) {
    const frameContexts: FrameContext[] = this.frameContext.appContext.frameContextManager.getFrameContextsByNamespace(this.namespace) || [];
    const matchedFrameContexts = frameContexts.filter((frameContext: FrameContext) => frameContext.viewModel && frameContext.viewModel.bindingPath.split('/').filter(p => p).toString() === bindingPath.split('/').filter(p => p).toString());
    let result = null;
    if (matchedFrameContexts) {
      matchedFrameContexts.every((frameContext: FrameContext) => {
        const frameId = frameContext.frameId;
        const componentsMap = this.frameContext.appContext.componentManager.getComponentsByFrameId(frameId);
        if (!componentsMap) {
          return true;
        }
        const datagridComponent = Array.from(componentsMap.values()).find((component: any) => component && component['__component_type__'] === 'DatagridComponent');
        if (datagridComponent) {
          result = datagridComponent;
          return false;
        } else {
          return true;
        }
      });
    }
    return result;
  }
  private getPropertyPaths(paths: string[]) {
    const tablePaths = ExpressionUtil.getAvailableChildrenPathsFromEntityPaths(paths, this.repository.entityTypeInfo);
    return paths.slice(tablePaths.length);
  }
  private isGridComponent(bindingPath: string) {
    const frameContexts: FrameContext[] = this.frameContext.appContext.frameContextManager.getFrameContextsByNamespace(this.namespace) || [];
    const frameContext = frameContexts.find((frameContext: FrameContext) => frameContext.viewModel && frameContext.viewModel.bindingPath.split('/').filter(p => p).toString() === bindingPath.split('/').filter(p => p).toString());
    if (frameContext) {
      return !!frameContext.viewModel['dataGridColumnsName'];
    } else {
      return false;
    }
  }
}