import { ANNOTATIONS, PROP_METADATA } from './decorator';
import { Translate } from '../../i18n';
//import { Cacheable, DefaultCacheProvider } from '../../cache';

/**
 * 
 * 
 * 1
 * 2
 */
class MetadataUtil {

  // ----------------------------------------
  // 
  // ----------------------------------------

  /**
   * 
   * 
   * [
   *   Injectable
   *   NgViewModel
   *   NgViewModel
   * ]
   */
  static getClassMetadatas(constructor: any): any[] {
    const metadatas = constructor[ANNOTATIONS];
    return metadatas;
  }

  /**
   * class
   * NgViewModel
   */
  static getClassMetadataByName(constructor: any, metadataName: string): any {
    const metadata = this.getClassMetadataByNameWithTranslate(constructor, metadataName, null, null);
    return metadata;
  }

  static getClassMetadataByNameWithTranslate(
    constructor: any, metadataName: string,
    translateService?: Translate, keysToTranslate?: string[]): any {
    const allClassMetadatas = this.getClassMetadatas(constructor);
    if (!allClassMetadatas) {
      return null;
    }
    const metadata = allClassMetadatas.find((classMetadata: any) => {
      return classMetadata.ngMetadataName === metadataName;
    });
    this.translateMetadataByName(metadata, translateService, keysToTranslate);
    return metadata;
  }

  // ----------------------------------------
  // 
  // ----------------------------------------

  /**
   * 
   * 
   * {
   *   propName1: [ NgDefaultValue, NgMaxLength, NgMinLength],
   *   propName2: [ NgDefaultValue, NgMaxLength, NgMinLength]
   * }
   */
   // @Cacheable({ key: ((context: any, args: any[]) => args[0]), provider: new DefaultCacheProvider() })
  static getPropsMetadatas(constructor: any): any {
    const allPropMetadatas = constructor[PROP_METADATA];
    return allPropMetadatas;
  }

  /**
   * 
   * 
   * 
   * {
   *    propName1: NgDefaultValue,
   *    propName2: NgDefaultValue
   * }
   */
  // @Cache({ provider: new MemoryCacheProvider(new MetadataKeyCompare()), key: ((context: any, args: any[]) => { return { constructor: args[0], metadataName: args[1] } }) })
  static getPropsMetadatasByName(constructor: any, metadataName: string): { [propName: string]: any } {
    const metadatas = this.getPropsMetadatasByNameWithTranslate(constructor, metadataName);
    return metadatas;
  }
  static getPropsMetadatasByNameWithTranslate(
    constructor: any, metadataName: string,
    translateService?: Translate, keysToTranslate?: string[]): { [propName: string]: any } {
    const metadatas = {};
    // 
    const allPropMetadatas = this.getPropsMetadatas(constructor);
    if (!allPropMetadatas) {
      return metadatas;
    }
    // 
    Object.keys(allPropMetadatas).forEach((propName: string) => {
      // 
      const propMetadatas: any[] = allPropMetadatas[propName];
      // 
      const metadata = propMetadatas.find((propMetadata: any) => {
        return propMetadata.ngMetadataName === metadataName;
      });
      if (metadata) {
        metadatas[propName] = metadata;
      }
    });
    // 
    this.translateMetadatasByName(metadatas, translateService, keysToTranslate);
    return metadatas;
  }

  static translateMetadatasByName(metadatas: any, translateService?: Translate, keysToTranslate?: string[])
    : { [propName: string]: any } {
    Object.keys(metadatas).forEach((propName: string) => {
      // 
      this.translateMetadataByName(metadatas[propName], translateService, keysToTranslate);
    });
    return metadatas;
  }

  static translateMetadataByName(metadata: any, translateService?: Translate, keysToTranslate?: string[]): any {
    if (metadata && translateService && keysToTranslate) {
      keysToTranslate.forEach((metadataPropKey) => {
        const propertyVariable: string = metadata[metadataPropKey];
        try {
        if (propertyVariable && propertyVariable.startsWith('{{') && propertyVariable.endsWith('}}')) {
          const translateKey = propertyVariable.replace('{{', '').replace('}}', '').trim();
          metadata[metadataPropKey] = translateService.transform(translateKey, null);
        }
        } catch (e) {
          console.info(e);
        }
      });
    }
    return metadata;
  }

  /**
   * 
   * [ NgDefaultValue, NgMaxLength, NgMinLength]
   */
  static getPropMetadatasByName(constructor: any, propName: string): any[] {
    // 
    return null;
  }

  /**
   * 
   * NgDefaultValue
   */
  static getPropMetadataByName(constructor: any, propName: string, metadataName: string): any {
    // 
    return null;
  }


  // ----------------------------------------
  // 
  // ----------------------------------------

}

export { MetadataUtil };
