/**
 * 
 */
export class Modification {

  /**
   * [1, 'name']
   *  1 ID name 
   */
  path?: string[];

  /**
   * 
   */
  preValue?: any;

  /**
   * 
   *
   * value  type()type 
   * - Add: valuejson 
   * - ValueChange: valuestring\number\boolean
   * - Remove: value{[key:string]: value} key
   *
   */
  value: any;

  /**
   * 
   */
  type: ModifyType;

  /**
   * 
   */
  isValid?: boolean;

  /**
   * 
   */
  errors?: { [type: string]: string };

  /**
   * 
   */
  position?: 1 | -1;

  /**
   * 
   */
  changeSetValue?: any;
  /**
   * 
   */
  isReset?: boolean;
  entityCreate?: boolean;
  dynamic?: boolean;
  /**
   * 
   * @param value 
   * @param modifyType 
   * @param path 
   * @param preValue 
   */
  constructor(value: any, modifyType: ModifyType, path?: string[], preValue?: any, position?: 1 | -1) {
    this.type = modifyType;
    this.value = value;
    this.preValue = preValue;
    this.path = path;
    this.position = position;
  }
}

/**
 * 
 */
export enum ModifyType {

  /**
   * 
   */
  Add = 'ADD',
  /**
   * 
   */
  AddData = 'AddData',
  /**
   * 
   */
  Clone = 'CLONE',
  /**
   * 
   */
  Remove = 'REMOVE',
  RemoveData = 'RemoveData',

  /**
   * 
   */
  ValueChange = 'VALUE_CHANGE',

  /**
   * 
   */
  Load = 'LOAD',

  /**
   * 
   */
  UnChanged = 'UNCHANGED',

  /**
   * 
   */
  PaginationInfoChange = 'PAGINATION_INFO_CHANGE',

  /**
   * 
   */
  Insert = 'Insert',
  /**
   * 
   */
  Update = 'UPDATE'
}
