import { Subject, Observable, from } from 'rxjs';
import { ChangeSet } from '../changeset/change_set';
import { Modification, ModifyType } from '../changeset/types';
import { Entity } from './entity';
import { EntityFactory } from './entity_creator';
import { PARENT_CLASS, PARENT_PATH, ClassType } from './types';
import { Validator } from './validator/validator';
import { ValidationResult } from './validator/types';

export interface IList<T> {
  [index: number]: T;
}
/**
 * 
 */
export class EntityList<T extends Entity> implements IList<T>, Iterable<T> {
  public __type__ = 'EntityList';

  // #region 
  private originalData: any[] = [];
  /**
   * 
   */
  private rawData: T[];

  /**
   * 
   */
  private listChanged = new Subject<Modification>();

  /**
   * 
   */
  private changeSet = new ChangeSet();

  /**
   * 
   */
  private validator = new Validator<T>();

  // #endregion


  // #region 

  /**
   * ()
   * @event
   */
  public onListChanged = this.listChanged.asObservable();

  /**
   * 
   */
  public get items(): T[] {
    return this.rawData;
  }

  /**
   * 
   */
  public get changes() {
    return this.changeSet.changes;
  }

  /**
   * 
   */
  [index: number]: T;

  /**
   * 
   */
  *[Symbol.iterator](): Iterator<T> {
    yield* this.items;
  }

  // #endregion


  /**
   * @param data JSON
   * @param type 
   */
  constructor(data?: any[], type?: ClassType) {
    this.clear();
    if (data) {
      // this.loadEntities(data);
      data.forEach(item => {
        this.initEntity(EntityFactory(type, item));
      });
    }
  }


  // #region 

  /**  */
  public loadEntities(entities: T[]) {
    this.clear();

    entities.forEach(entity => {
      this.initEntity(entity);
    });

    // Load
    const changeItem = {
      path: [],
      value: entities,
      preValue: undefined,
      type: ModifyType.Load,
      target: this
    };
    this.setChanges(changeItem);
  }
  /**
   * 
   */
  public clear() {
    this.rawData = [];
    this.originalData = [];
  }

  /**
   * 
   * @param entity 
   * @param isCloned 
   */
  public appendNew(entity: T, isCloned: boolean = false): T {
    const newEntity = this.initEntity(entity, true);
    // 
    const changeItem = {
      path: [],
      value: [newEntity],
      preValue: undefined,
      type: ModifyType.Add
    };
    if (isCloned === true) {
      changeItem.type = ModifyType.Clone;
    }
    this.setChanges(changeItem);
    return newEntity;
  }
  /**
   * 
   * @param entity 
   * @param position 
   */
  public insert(entity: T, position?: 1 | -1): T {
    const newEntity = this.initEntity(entity, true);

    // 
    const changeItem = {
      path: [],
      value: [newEntity],
      preValue: undefined,
      type: ModifyType.Insert,
      position: position,
    };

    this.setChanges(changeItem);
    return newEntity;
  }
  /**
   * 
   */
  public appendEntity(entity: T): void {
    const newEntity = this.initEntity(entity, true);
    // 
    const changeItem = {
      path: [],
      value: [newEntity],
      preValue: undefined,
      type: ModifyType.Add
    };

    this.setChanges(changeItem);
  }

  /**
   * 
   */
  public appendEntities(entities: T[]): void {
    const newEntites = entities.map((entity: T) => {
      return this.initEntity(entity, true);
    });
    const changeItem = {
      path: [],
      value: newEntites,
      preValue: undefined,
      type: ModifyType.Add
    };
    this.setChanges(changeItem);
  }

  /**
   * ID true false 
   * @param primaryId ID
   */
  public remove(primaryId: string): boolean {
    const total = this.count();
    const indexToRemove = this.rawData.findIndex((entity: Entity) => {
      return entity.primaryValue === primaryId;
    });
    if (indexToRemove === -1) {
      return false;
    }
    const entityToRemove = this.rawData[indexToRemove];
    this.rawData.splice(indexToRemove, 1);

    // 
    const changeItem = {
      path: [],
      value: { [entityToRemove.primaryProperty.dataField]: primaryId },
      preValue: undefined,
      type: ModifyType.Remove
    };

    this.updateIndex(total);
    this.setChanges(changeItem);

    return true;
  }

  /**
   * ID
   * @param id 
   */
  public get(id: string) {
    return this.items.find(item => {
      return item.primaryValue === id;
    });
  }

  /**
   * 
   * @param value 
   */
  public setChanges(modinfo: Modification) {

    // app
    this.listChanged.next(modinfo);

    // changeSetchagne
    const change = Object.assign({}, modinfo);
    if ((modinfo.type === ModifyType.Add || modinfo.type === ModifyType.Insert || modinfo.type === ModifyType.Clone) && modinfo.value[0] instanceof Entity) {
      change.value = [modinfo.value[0].data];
    }
    this.changeSet.append(change);
  }

  /**  */
  public count() {
    return this.items.length;
  }

  /**
   * 
   */
  public indexOf(entity: T): number {
    return this.items.indexOf(entity);
  }

  /**
   * 
   * @param propertyName 
   */
  public sum(propertyName: string): number {
    if (this.count() === 0) {
      return 0;
    }
    return this.items.reduce((val, curr: T) => {
      return val + curr[propertyName];
    }, 0);
  }

  /**
   * 
   */
  public validate(): Observable<ValidationResult> {
    const propertyName = this.getPropertyName();
    return from(this.validator.validate(this[PARENT_CLASS], propertyName));
  }

  /**
   * toJSON
   * @deprecated
   */
  public toJson() {
    return this.rawData;
  }

  /**
   * JSON
   */
  public toJSON(): any[] {
    const result = [];
    this.items.forEach((entity: Entity) => {
      result.push(entity.toJSON());
    });
    return result;
  }

  public toArray(): T[] {
    return this.items;
  }

  // #endregion


  // #region 

  /**
   * 
   * @param entity 
   */
  private initEntity(entity: T, isNewEntity: boolean = false): T {
    entity[PARENT_CLASS] = this;
    entity[PARENT_PATH] = this[PARENT_PATH];
    entity.onValueChanged.subscribe((v: Modification) => {
      const path = v.path;
      const value = v.value;
      const preValue = v.preValue;
      const operator = v.type;
      const subChanges = { path, value, preValue, type: operator };
      if (v.changeSetValue !== undefined) {
        subChanges['changeSetValue'] = v.changeSetValue;
      }
      this.setChanges(subChanges);
    });
    // TODO: 
    const newLength = this.rawData.push(entity);
    this[newLength - 1] = entity;
    if (!isNewEntity) {
      this.originalData.push(entity.toJSON());
    }
    return entity;
  }

  /**
   * 
   * @param total 
   */
  private updateIndex(total: number) {
    for (let i = 0; i < total; i++) {
      delete this[i];
    }
    this.rawData.forEach((entity, index) => {
      this[index] = entity;
    });
  }

  /**
   * 
   */
  private getPropertyName() {
    const path = this[PARENT_PATH];
    if (path && path.length) {
      const name = path[path.length - 1];
      return name;
    }
    return undefined;
  }

  // #endregion

}
