import { Injectable } from "@angular/core";
import { IResolver } from "./types";
/**
 * 
 */
@Injectable()
export class CommentDependencyResolver implements IResolver {
  public resolve(expr: string): string[] {
    const dependencies = [];
    if (!expr || expr.length < 1) {
      return dependencies;
    }
    const defineRegex = /\/\*\*\s*__define__\((.*)\)\s*\*\//;
    const defineMatchArray: RegExpMatchArray = expr.match(defineRegex);
    if (defineMatchArray && defineMatchArray.length === 2) {
      const defineString: string = defineMatchArray[1].trim();
      let defineObject = null;
      try {
        defineObject = JSON.parse(defineString);
      } catch (e) { console.warn(`${defineString}`); }
      if (defineObject && defineObject.hasOwnProperty('deps') && Array.isArray(defineObject['deps'])) {
        dependencies.push(...defineObject['deps']);
      }
    }
    return dependencies;
  }
}