import { FrameContext } from '../frame/index';
import { FrameContextManager } from './frame_contex_manager';

/**
 * 
 */
class ChangeDetectionController {

  /**
   * FrameContext
   */
  private get frameContexts(): FrameContext[] {
    return this.frameContextManager.getFrameContexts();
  }

  /**
   * 
   */
  constructor(private frameContextManager: FrameContextManager) {
  }

  /**
   * FrameComponent
   * @todo
   * 1
   * 2setTimeoutsubscribe
   * 3setTimeout
   */
  public reattach(): void {
    setTimeout(() => {
      this.frameContexts.forEach((frameContext: FrameContext) => {
        frameContext.frameComponent.reattach();
        frameContext.frameComponent.detectChanges();
      });
    });
  }

  /**
   * FrameComponent
   */
  public detach(): void {
    this.frameContexts.forEach((frameContext: FrameContext) => {
      frameContext.frameComponent.detach();
    });
  }
}

export { ChangeDetectionController };
