import { Injectable } from '@angular/core';
import { VariableParser } from './variable_parser';
import { AppContext } from '../app/index';
import { ParseUtil } from './parse_util';
import { FrameContext } from '../frame';
const VARIABLE_MATCHER = /#{\S+?}/g;
/**
 * 
 */
@Injectable()
class FrameIdVariableParser implements VariableParser {
  public context: any;
  /**
   * 
   * @param expression /#{root-component}/stateName
   * @param context 
   */
  public parse(expression: string, context: any): any {
    this.context = context;
    const variables = this.extractVariables(expression);

    if (!variables || variables.length < 1) {
      return expression;
    }
    variables.forEach((exp: string) => {
      const frameId = this.getVariableValue(exp);
      expression = expression.replace(VARIABLE_MATCHER, frameId);
    });
    return expression;
  }
  /**
   * 
   * @param expression like #{root-component}
   */
  private getVariableValue(expression: string) {
    const relativeFrameId = expression.substring(2, expression.length - 1);
    return this.getFullFrameId(relativeFrameId);
  }
  private extractVariables(expression: string) {
    if (!expression) {
      return [];
    }
    return expression.match(VARIABLE_MATCHER);
  }

  /**
   * frameId
   * @param frameId frameId
   */
  private getFullFrameId(frameId: string) {
    const frameContext: FrameContext = ParseUtil.getFrameContext(this.context);
    const namespace = frameContext.namespace || '';
    const prefix = namespace ? `${namespace}_` : '';
    return `${prefix}${frameId}`;
  }
}

export { FrameIdVariableParser };
