
import { IFormViewModel } from './form-metadata';
import { Schema, SchemaEntity, SchemaEntityField } from './schema';

export class SchemaService {

    constructor() { }

    /**
     * 
     * @param fieldIds 
     * @param schema 
     * @param viewModel 
     * @returns Map
     */
    public getFieldsByIds(fieldIds: string[], schema: Schema, viewModel: IFormViewModel)
        : Map<string, SchemaEntityField> {
        const specifiedFieldsMap = new Map<string, SchemaEntityField>();
        const entities = schema.entities;
        if (entities && entities.length && viewModel) {
            const entityPath = viewModel.bindTo;
            const fields = this.getEntityFields(entities, entityPath);
            const fieldsMap = this.flattenFields(fields);
            fieldIds.forEach(fieldId => {
                if (fieldsMap.has(fieldId)) {
                    specifiedFieldsMap.set(fieldId, fieldsMap.get(fieldId));
                }
            });
        }
        return specifiedFieldsMap;
    }

    /**
     * 
     * @param fields 
     * @param fieldsMap Map
     * @returns Map
     */
    private flattenFields(
        fields: SchemaEntityField[],
        fieldsMap: Map<string, SchemaEntityField> = new Map<string, SchemaEntityField>()
    ): Map<string, SchemaEntityField> {
        for (const field of fields) {
            fieldsMap.set(field.id, field);
            if (field.type && field.type.fields && field.type.fields.length > 0) {
                this.flattenFields(field.type.fields, fieldsMap);
            }
        }
        return fieldsMap;
    }

    /**
     * 
     * @param entities 
     * @param entityPath '/'
     */
    private getEntityFields(entities: SchemaEntity[], entityPath: string): SchemaEntityField[] {
        if (entities && entities.length) {
            const splitIndex = entityPath.indexOf('/');
            if (splitIndex > -1) {
                entityPath = entityPath.slice(splitIndex + 1, entityPath.length);
            }
            // 
            for (const entity of entities) {
                if (entityPath === '' || entityPath === entity.code || entityPath === entity.label) {
                    return entity.type.fields;
                } else {
                    const fields = this.getEntityFields(entity.type.entities, entityPath);
                    if (fields && fields.length) {
                        return fields;
                    }
                }
            }
        }
        return [];
    }
}
