export class DomService {

  constructor() { }

  public getElementByBinding(contents: any[], bindingFieldId: string): any[] {
    const controls = [];
    for (const element of contents) {
      // 
      if (element.fields) { // 
        const childControls = this.getElementByBinding(element.fields, bindingFieldId);
        controls.push(...childControls);
      }
      // 
      else if (element.contents) { // 
        const childControls = this.getElementByBinding(element.contents, bindingFieldId);
        controls.push(...childControls);
      }
      // 
      else if (element.editor) {
        const childControls = this.getElementByBinding([element.editor], bindingFieldId);
        controls.push(...childControls);
      }
      // 
      else if (element.binding && element.binding.field === bindingFieldId) {
        controls.push(element);
      }
    }
    return controls;
  }
}
