import { BehaviorSubject } from 'rxjs';
import { MetadataUtil } from '../metadata/index';
import { StateMachineContext } from './context';
import { NgState, NgAction, NgRenderState } from './decorators';
import {
  State, initialUIState, Effect, Render,
  StateDictionary, RenderStateDictionary, RenderDictionary
} from './types';
import { FrameContext } from '../frame/index';
import { StateMachineEvent } from './state_machine_event';
import { VariableParseService } from '../variable/variable_parse_service';
import { OnDestroy } from '@angular/core';
import { IDisposable } from '../core';

/**
 * 
 */
export interface StateMachineOption {

  /**
   * 
   */
  renders?: { [index: string]: Render };

  /**
   * 
   */
  states?: string[];

  /**
   * 
   */
  effects?: { [index: string]: Effect };
}

/**
 * 
 */
export const effectHandlers = {

  /**
   * 
   */
  transit: {

    /**
     * 
     * @param stateMachine  
     * @param stateName     
     * @param preconditions 
     */
    // tslint:disable-next-line: only-arrow-functions
    perform: function (statemachine: StateMachine, stateName: string, preconditons: any[] = []) {
      const nextState: State = statemachine.states[stateName];
      statemachine.context.transitTo(nextState.name);
      statemachine.render();
    }
  }
};

/**
 * 
 *
 * ### 
 * 
 * - State
 * - RenderState
 * - Action
 *
 * ### 
 *
 * ****
 *
 * - StateMachineNgStatemachine
 * - 
 *
 * ****
 *
 * - NgStatemachine
 * - NgStateinitialState
 * - NgRenderStaterender
 *   
 * - NgActiontransitTo
 *
 * ```ts
 * @Injectable()
 * @NgStatemachine()
 * class SimpleStateMachine extends StateMachine {
 *
 *   // 
 *   @NgState({ initialState: true })
 *   viewState: State;
 *
 *   // 
 *   @NgState()
 *   editState: State;
 *
 *   // 
 *   @NgRenderState({
 *     render: (context) => context.state === 'viewState'
 *   })
 *   canEdit: RenderState;
 *
 *   // 
 *   @NgRenderState({
 *     render: (context) => context.state === 'editState'
 *   })
 *   canSave: RenderState;
 *
 *   // 
 *   @NgRenderState({
 *     render: (context) => context.state === 'editState'
 *   })
 *   canInput: RenderState;
 *
 *   // 
 *   @NgAction({ transitTo: 'editState' })
 *   edit: Action;
 *
 *   // 
 *   @NgAction({ transitTo: 'viewState' })
 *   view: Action;
 * }
 * ```
 * 
 * - 
 * - 
 * - viewedit
 *
 *
 * ### 
 *
 * 
 *
 * ```html
 * <button type="button" [disabled]="!viewModel.stateMachine.canEdit"></button>
 * <button type="button" [disabled]="!viewModel.stateMachine.canSave"></button>
 * <input id="code" [disabled]="!viewModel.stateMachine.canInput" />
 * <input id="name" [disabled]="!viewModel.stateMachine.canInput" />
 * ```
 *
 * ### 
 * 
 * 
 * CommandHandler
 * ```ts
 * @Injectable()
 * @NgCommandHandler({
 *   commandName: 'save'
 * })
 * class SaveHandler extends CommandHandler {
 *
 *   schedule() {
 *     this.addTask('save', () => {
 *       // 
 *     });
 *
 *     // 
 *     this.addTask('transitState', ) => {
 *       this.stateMachine['view']();
 *     });
 *   }
 * }
 * ```
 */
export class StateMachine implements OnDestroy, IDisposable {
  private isStateInited = false;
  /**
   * 
   */
  public initialState: State;

  /**
   * 
   */
  public states: StateDictionary;

  /**
   * 
   */
  public renderStates: RenderStateDictionary;

  /**
   * 
   */
  public renders: RenderDictionary;

  /**
   * 
   */
  public context: StateMachineContext;

  /**
   * 
   */
  public stateChange: BehaviorSubject<string>;

  public appContext: any;

  public frameContext: FrameContext;

  /**
   * 
   */
  public stateMachineEvent: StateMachineEvent;
  /**
   * 
   */
  public metadatas: { states: { [stateName: string]: NgState }, renderStates: { [renderStateName: string]: NgRenderState }, actions: { [actionName: string]: NgAction } };
  private isDisposed = false;
  /**
   * 
   */
  constructor() {
    const propsMetadatas = MetadataUtil.getPropsMetadatas(this.constructor);

    // build
    if (propsMetadatas) {
      Object.keys(propsMetadatas).forEach((propName: string) => {
        const propMetadatas = propsMetadatas[propName];
        propMetadatas.forEach(propMetadata => {
          this['build' + propMetadata.ngMetadataName](propName, propMetadata);
        });
      });
    }

    // if (!this.initialState) {
    //   throw new Error('NgState');
    // }

    this.stateChange = new BehaviorSubject<any>(false);
    this.context = new StateMachineContext(this);
    this.stateMachineEvent = new StateMachineEvent(this);
  }
  public dispose(options?: any) {
    this.isDisposed = true;
    this.frameContext = null;
    this.appContext = null;
    this.context = null;
    this.stateMachineEvent = null;
    this.metadatas = null;
  }
  public ngOnDestroy(): void {
    this.dispose();
  }

  // render
  initialize(frameContext: FrameContext, variableParseService: VariableParseService) {
    this.appContext = frameContext.appContext;
    this.frameContext = frameContext;
    const stateMachineMetadata = this.appContext.metadata.stateMachine || this.collectionMetadata();
    this.metadatas = stateMachineMetadata;
    this.buildStateMachine(stateMachineMetadata);
    // if (!this.initialState) {
    //   throw new Error('NgState');
    // }
    this.context.initialize(variableParseService, this.initialState);
    this.stateMachineEvent.initialize(this.frameContext);
    this.render();
  }

  private collectionMetadata(): {
    states: { [stateName: string]: NgState },
    renderStates: { [renderStateName: string]: NgRenderState },
    actions: { [actionName: string]: NgAction }
  } {
    const stateMachineMetadata = {
      states: {},
      renderStates: {},
      actions: {}
    };
    const propsMetadatas = MetadataUtil.getPropsMetadatas(this.constructor);
    if (propsMetadatas) {
      Object.keys(propsMetadatas).forEach((propName: string) => {
        const propMetadatas = propsMetadatas[propName];
        propMetadatas.forEach(propMetadata => {
          switch (propMetadata.ngMetadataName) {
            case 'NgState':
              stateMachineMetadata.states[propName] = propMetadata;
              break;
            case 'NgRenderState':
              stateMachineMetadata.renderStates[propName] = propMetadata;
              break;
            case 'NgAction':
              stateMachineMetadata.actions[propName] = propMetadata;
              break;
          }
        });
      });
    }
    return stateMachineMetadata;
  }

  private buildStateMachine(metadata: {
    states: { [stateName: string]: NgState },
    renderStates: { [renderStateName: string]: NgRenderState },
    actions: { [actionName: string]: NgAction }
  }) {
    Object.keys(metadata.states).forEach((stateName: string) => {
      this.buildNgState(stateName, metadata.states[stateName]);
    });

    Object.keys(metadata.renderStates).forEach((renderStateName: string) => {
      this.buildNgRenderState(renderStateName, metadata.renderStates[renderStateName]);
    });

    Object.keys(metadata.actions).forEach((actionName: string) => {
      this.buildNgAction(actionName, metadata.actions[actionName]);
    });
  }

  /**
   * 
   * @param stateName 
   * @param ngState   
   */
  private buildNgState(stateName: string, ngState: NgState) {
    this.states = this.states || {};
    this[stateName] = new State(stateName);
    this.states[stateName] = this[stateName];
    if (ngState.initialState) {
      this.initialState = this[stateName];
    }
  }

  /**
   * 
   * @param renderStateName 
   * @param ngRenderState   
   */
  private buildNgRenderState(renderStateName: string, ngRenderState: NgRenderState) {
    this.renderStates = this.renderStates || {};
    this[renderStateName] = initialUIState;
    this.renderStates[renderStateName] = this[renderStateName];

    // renderStaterenderrenders
    this.renders = this.renders || {};
    this.renders[renderStateName] = ngRenderState.render;
  }

  /**
   * 
   * @param actionName 
   * @param ngAction   
   */
  private buildNgAction(actionName: string, ngAction: NgAction) {
    this[actionName] = () => {
      effectHandlers.transit.perform(this, ngAction.transitTo, ngAction.precondition);
    };
  }

  /**
   * 
   *  staterenderrenderState
   */
  render() {
    if (this.isDisposed) {
      return;
    }
    for (const renderStateName in this.renderStates) {
      if (this.renderStates.hasOwnProperty(renderStateName) === false) {
        continue;
      }
      const stateRender = this.renders[renderStateName];
      if (!stateRender) {
        continue;
      }
      // renderrenderState
      this.renderStates[renderStateName] = stateRender(this.context);
      this[renderStateName] = this.renderStates[renderStateName];
    }
    this.stateChange.next(this.context.state);
  }
}
