import { Injectable, Injector } from '@angular/core';
import { MetadataUtil } from '../../metadata/index';
import { FrameContext, FrameComponent } from '../../frame/index';
import { UIState } from '../../ui-state/index';
import { NG_SUBSCRIPTION, NgSubscription, ParamMap } from './subscription_decorator';
import { IDisposable } from '../../core/index';

@Injectable()
class Subscription {

  /**
   * 
   */
  public init(frameComponent: FrameComponent): IDisposable[] {
    if (!frameComponent) {
      return;
    }

    return this.bindSubscription(frameComponent, null);
  }


  /**
   *  
   * @param frameComponent 
   * @param ngEvents 
   * @returns eventPipes
   */
  public initWithSubscriptions(frameComponent: FrameComponent, ngEvents: {
    [propName: string]: any;
  }): IDisposable[] {
    if (!frameComponent) {
      return;
    }

    return this.bindSubscription(frameComponent, ngEvents);
  }

  private bindSubscription(frameComponent: FrameComponent, ngEvents: {
    [propName: string]: any;
  }) {
    const context = frameComponent.context;
    if (!context) {
      return;
    }

    const ngEventHandlerProps = ngEvents ? ngEvents : this.getNgEvents(frameComponent);
    if (!ngEventHandlerProps) {
      return;
    }

    const eventPipes: IDisposable[] = [];
    Object.keys(ngEventHandlerProps).forEach((propertyName: string) => {
      const ngImportEvent: NgSubscription = ngEventHandlerProps[propertyName];

      // 
      const targetContext = context;

      const receiver = frameComponent;
      const emitter = ngImportEvent.token;
      const tokenValue = ngImportEvent.token;
      const eventName = ngImportEvent.name;

      const paramMapCollection = ngImportEvent.paramMapCollection;
      const eventPipe = targetContext.eventBus.on(emitter, tokenValue, eventName, receiver,
        (eventArgs) => {
          this.subscriptionHandler(eventArgs, paramMapCollection, targetContext);

          const eventHandler: Function = frameComponent[eventName];
          if (!eventHandler) {
            return;
          }

          try {
            eventHandler(receiver, eventArgs);
          } catch {
            throw new Error('Error invoking method ' + eventName);
          }
        }
      );

      eventPipes.push(eventPipe);
    });

    return eventPipes;
  }

  /**
   * 
   * @param frameComponent component
   * @returns 
   */
  public getNgEvents(frameComponent: FrameComponent) {
    return MetadataUtil.getPropsMetadatasByName(frameComponent.constructor, NG_SUBSCRIPTION);
  }

  private subscriptionHandler(param: any, paramMapCollection: ParamMap[], currentFrameContext: FrameContext) {

    if (!param || !paramMapCollection || paramMapCollection.length <= 0 || !currentFrameContext) {
      return;
    }

    this.paramMap2UiState(param, paramMapCollection, currentFrameContext);
  }

  /**
   * paramMapparamUISTATE
   */
  private paramMap2UiState(param: any, paramMapCollection: ParamMap[], currentFrameContext: FrameContext) {
    for (let i = 0; i < paramMapCollection.length; i++) {
      const from = paramMapCollection[i].from;
      const frameId = paramMapCollection[i].frameId;
      const to = paramMapCollection[i].to;

      if (!from || !frameId || !to) {
        continue;
      }
      const destContext: FrameContext = this.getFrameContext(frameId, currentFrameContext);
      if (destContext == null) {
        continue;
      }
      this.setUiStateProperty(to, param[from], destContext.uiState);
      // this.setUiStateProperty(to, param[from], currentFrameContext.uiState);
    }
  }

  private getFrameContext(targetFrameContextId: string, currentContext: FrameContext): FrameContext {
    let destContext: FrameContext = null;
    try {
      destContext = currentContext.appContext.getFrameContext(targetFrameContextId);
    } catch {
      throw new Error('Error in Getting FrameContext');
    }
    return destContext;
  }

  private setUiStateProperty(propertyName: string, propertyValue: string, uiState: UIState) {
    try {
      uiState.setPropertyValue(propertyName, propertyValue);
    } catch {
      throw new Error("Error in Setting Property Value of the current UISTATE" + uiState);
    }
  }

}

export { Subscription };
