import { ICacheObject, ICacheProvider, Key, TTL } from "../types";
import { CacheObject } from "./cache_object";

export class CacheContainer<T>{
  constructor(protected provider: ICacheProvider<T>) {
    this.provider = provider;
  }
  public get(key: Key): T {
    const cacheObject: ICacheObject<T> = this.provider.get(key);
    if (!cacheObject) {
      return undefined;
    }
    const isExpired = this.isCacheObjectExpired(cacheObject);
    return isExpired ? undefined : cacheObject.content;
  }
  public set(key: Key, value: T, ttl?: TTL) {
    const cacheObject: ICacheObject<T> = new CacheObject(key, value, ttl || 0);
    this.provider.set(cacheObject);
  }
  private isCacheObjectExpired(cacheObject: ICacheObject<T>) {
    if (typeof cacheObject.ttl === 'number') {
      return Date.now().valueOf() > cacheObject.createAt.valueOf() + cacheObject.ttl;
    } else {
      return Date.now() > cacheObject.ttl.valueOf();
    }
  }
}