export type Type = (...args) => any;
export type TTL = number | Date;
export type Key = string | number | ICacheKey | ICacheKeyBuilder;
export interface ICacheKeyBuilder<T = any[], U = any> {
  (context: U, args: T): any;
}
export enum CacheReturnType {
  Static = 1,
  Promise
}
export interface ICacheKey { }
export interface ICacheKeyCompare {
  compare(key1: ICacheKey, key2: ICacheKey): boolean;
}
export class CacheKeyCompare implements ICacheKeyCompare {
  compare(key1: string | number, key2: string | number) {
    return key1 === key2;
  }
}
export interface ICacheOptions<T> {
  key?: Key;
  // returnType?: CacheReturnType;
  ttl?: TTL;
  cacheKeyCompare?: ICacheKeyCompare;
  provider: ICacheProvider<T>
}
export interface ICacheObject<TValue> {
  key: Key,
  content: TValue,
  ttl: TTL,
  createAt: Date,
  isExpired(): boolean;
}
export interface ICacheProvider<TValue> {
  has(key: Key): boolean;
  length(): number;
  set(value: ICacheObject<TValue>);
  get(key: Key): ICacheObject<TValue>;
  delete(key: Key);
  clear();
  keys(): IterableIterator<Key>;
  values(): IterableIterator<ICacheObject<TValue>>;
}
