import { Type } from '@angular/core';
//import { Map as ImmutableMap } from 'immutable';
import { Change } from './changes';
import { BindingProperty, BindingPropertyType } from './binding_property';
import { BindingList } from './binding_list';
import { PropertyUtil } from './property_util';
import { BaseBindingObject } from './base_binding_object';
import { BindingListFactory } from './binding_list_factory';
import { ClassType } from '../entity';
import { BindingObject } from './binding_object';
import { TranslateService } from '../i18n/translate_service';
// import { BindingObjectFactory } from './binding_object_factory';

/**
 * BindingObjectTypeFactory
 */
export class BindingObjectTypeFactory {
  private static provider: Map<BindingProperty[], Type<BindingObject>> = new Map<BindingProperty[], Type<BindingObject>>();
  /**
   * BindingObject
   * @param properties 
   * @param data 
   * @returns 
   */
  public static create(properties: BindingProperty[]/*, data?: any*/) {
    const bindingObjectType = this.getType(properties);
    return new bindingObjectType();
  }
  /**
   * 
   * @param properties 
   * @returns 
   */
  private static createType(properties: BindingProperty[]): ClassType<BindingObject> {
    // 
    const bindingObjectType = class BindingObjectType extends BaseBindingObject {
      constructor() {
        super();
        // this.innerValues = ImmutableMap(Object.assign({}, data));

      }
      //#endregion load

      /*
      public load(data: any) {
        // data
        this.innerValues = ImmutableMap(Object.assign({}, data));
        this.properties.forEach((property: BindingProperty) => {
          if (property.type === BindingPropertyType.List) {
            this.loadLists(property);
          } else if (property.type === BindingPropertyType.Object) {
            this.loadObjects(property);
          } else if (property.type === BindingPropertyType.Dynamic) {
            this.loadDynamicObjects(property);
          } else {
            this.loadFields(property);
          }
        });
      }
      private loadFields(property: BindingProperty) {
        const propertyName = property.name;
        const dataField = property.dataField || propertyName;
        let value;
        if (property.enableMultiLangInput) {
          value = this.getValue(dataField, false);
        } else {
          value = this.getValue(propertyName);
        }
        this[propertyName] = value;
      }
      private loadLists(property: BindingProperty) {
        const propertyName = property.name;
        const key = `_${propertyName}_`;
        const bindingList: BindingList = this[key];
        if (bindingList) {
          const childListProperties = PropertyUtil.getProperties(property.entityType);
          const data: any[] = this.getValue(propertyName);
          if (data) {
            const bindingObjects = data.map(item => {
              const bindingObject = BindingObjectTypeFactory.create(childListProperties);
              return bindingObject;
            });
            bindingList.load(bindingObjects);
          }
        }
      }
      private loadObjects(property: BindingProperty) {
        const propertyName = property.name;
        const key = `_${propertyName}_`;
        const value = this.getValue(propertyName) || {};
        const childObjectProperties = PropertyUtil.getProperties(property.entityType);
        const bindingObject = BindingObjectTypeFactory.create(childObjectProperties);
        this[key] = bindingObject;

      }
      private loadDynamicObjects(property: BindingProperty) {
        const propertyName = property.name;
        const value = this.getValue(propertyName) || {};
        const dynamicObject = BindingObjectFactory.createDynamicBindingObject(value);
        Object.defineProperty(this, propertyName, {
          value: dynamicObject
        });
      }*/
      //#endregion
    };
    // 
    const primaryKey = PropertyUtil.getPrimaryKey(properties);
    // 
    bindingObjectType.prototype.primaryKey = primaryKey;
    bindingObjectType.prototype.properties = properties;
    // 
    this.extendProperties(bindingObjectType.prototype, properties);
    return bindingObjectType;
  }
  /**
   * 
   * @param typePrototype 
   * @param properties 
   */
  private static extendProperties(typePrototype: BaseBindingObject, properties: BindingProperty[]) {
    // BindingObject
    properties.forEach((property: BindingProperty) => {
      if (property.type === BindingPropertyType.List) {
        this.extendListProperty(typePrototype, property);
      } else if (property.type === BindingPropertyType.Object) {
        this.extendObjectProperty(typePrototype, property);
      } else if (property.type === BindingPropertyType.Dynamic) {
        this.extendDynamicObjectProperty(typePrototype, property);
      } else {
        this.extendPlainProperty(typePrototype, property);
      }
    });
  }
  /**
   * 
   * @param typePrototype 
   * @param property 
   */
  private static extendListProperty(typePrototype: BaseBindingObject, property: BindingProperty) {
    const propertyName = property.name;
    const childListProperties = PropertyUtil.getProperties(property.entityType);
    const key = `_${propertyName}_`;
    // BindingList
    Object.defineProperty(typePrototype, propertyName, {
      get: function () {
        let bindingList = this[key];
        if (!bindingList) {
          bindingList = BindingListFactory.create(childListProperties);
          this[key] = bindingList;
          // 
          const data: any[] = this.getValue(propertyName);
          if (data) {
            const bindingObjects = data.map(item => {
              const bindingObject = BindingObjectTypeFactory.create(childListProperties);
              return bindingObject;
            });
            bindingList.load(bindingObjects);
          }
          // ListparentListchanges
          bindingList.parent = this;
          bindingList.changes.subscribe((change: Change) => {
            change.path.unshift(propertyName);
            this.changes.next(change);
          });
        }
        return bindingList;
      },
      set: function (bindingList: BindingList) {
        this[key] = bindingList;
      }
    });
  }
  /**
   * 
   * @param typePrototype 
   * @param property 
   */
  private static extendObjectProperty(typePrototype: BaseBindingObject, property: BindingProperty): void {
    const propertyName = property.name;
    const childObjectProperties = PropertyUtil.getProperties(property.entityType);
    const key = `_${propertyName}_`;
    Object.defineProperty(typePrototype, propertyName, {
      get: function () {
        let bindingObject = this[key];
        if (!bindingObject) {
          const value = this.getValue(propertyName) || {};
          bindingObject = BindingObjectTypeFactory.create(childObjectProperties);
          this[key] = bindingObject;
          // ObjectparentObjectchanges
          bindingObject.parent = this;
          bindingObject.changes.subscribe((change: Change) => {
            change.path.unshift(propertyName);
            this.changes.next(change);
          });
        }
        return bindingObject;
      },
      set: function (value: BaseBindingObject) {
        this[key] = value;
      }
    });
  }
  /**
   * 
   * @param typePrototype 
   * @param property 
   */
  private static extendDynamicObjectProperty(typePrototype: BaseBindingObject, property: BindingProperty): void {
    const propertyName = property.name;
    // Object.defineProperty(typePrototype, propertyName, {
    //   value: null
    // });
    typePrototype[propertyName] = null;
  }
  /**
   * 
   * @param typePrototype 
   * @param property 
   */
  private static extendPlainProperty(typePrototype: BaseBindingObject, property: BindingProperty): void {
    const propertyName = property.name;
    Object.defineProperty(typePrototype, propertyName, {
      get: function () {
        if (property.enableMultiLangInput === true) {
          let value = this.getValue(propertyName, false);
          if (!value) {
            value = this.getValue(propertyName, false);
            const langCode = TranslateService.getCurrentLanguage();
            return { [langCode]: value };
          }
          return value;
        } else {
          const value = this.getValue(propertyName);
          return value;
        }
      },
      set: function (value: any) {
        const oldValue = this.getValue(propertyName);
        if (value === oldValue) {
          return;
        }
        this.setValue(propertyName, value, true, true);
      }
    });
  }
  /**
   * bindingList
   * @param properties bindingList
   * @returns 
   */
  private static getType(properties: BindingProperty[]): Type<BindingObject> {
    if (this.provider.has(properties)) {
      return this.provider.get(properties);
    }
    const bindingObjectType = this.createType(properties);
    this.provider.set(properties, bindingObjectType);
    return bindingObjectType;
  }
}