import { UserSettings } from "./types";
export class UserSettingsService {
  private static userSettings: UserSettings = null;
  private static timeZone: string = undefined;
  private static timeZoneOffset: number = undefined;
  public static setUserSettings(userSettings: UserSettings) {
    this.userSettings = userSettings;
    this.timeZone = undefined;
    this.timeZoneOffset = undefined;
  }
  /**
   * 
   */
  public static getTimeZone() {
    if (this.timeZone !== undefined) {
      return this.timeZone;
    }
    const timeZone = this.userSettings && this.userSettings.timeZone || null;
    this.timeZone = timeZone;
    return timeZone;
  }
  public static getTimeZoneOffset() {
    if (this.timeZoneOffset !== undefined) {
      return this.timeZoneOffset;
    }
    const timeZoneOffset = this.userSettings && this.userSettings.timeZoneOffset || null;
    this.timeZoneOffset = timeZoneOffset;
    return timeZoneOffset;
  }
}