import { Inject, Injectable, Injector } from '@angular/core';
import { Expression } from '../expression/index';
import { NAMESPACE } from '../frame/index';
import { MESSAGE_SERVICE_TOKEN, NOTIFY_SERVICE_TOKEN, IMessageService, INotifyService } from '../core/index';

@Injectable()
export class RelativeEffector implements Expression.Effector {
  public ns: string;
  constructor(private injector: Injector, @Inject(MESSAGE_SERVICE_TOKEN) private messageService: IMessageService, @Inject(NOTIFY_SERVICE_TOKEN) private notifyService: INotifyService, @Inject(NAMESPACE) private namespace) {
    this.ns = namespace;
  }

  public effect(path: string, value: any, options: Expression.EffectOptions) {
    if (value === true && options.message && this.notifyService) {
      const messageType = options.messageType || 'info';
      this.notifyService[messageType](options.message, { hideTitle: true });
    }
  }
}