import { Subject, Subscription } from 'rxjs';
import { IDisposable } from '../core/index';
import { EventTypeEnum } from '../event-mechanism';
import { EventPipeType } from './event-pipe-type';

export class EventPipe implements IDisposable {
  private lastEventId = -1;
  /**
   * 
   */
  private eventSubject: Subject<any>;

  /**
   * Map
   * key=FrameComponent
   * valueevetnSubjectSubscriptionrxjs
   */
  private subscriptionMap: Map<object, Subscription>;

  /**
   * 
   */
  private onceSubscriptionMap: Map<object, Subscription>;

  /**
   * EventPipe
   */
  public eventPipeType = EventPipeType.Compile; 
  public get subscriptions(): Map<object, Subscription> {
    return this.subscriptionMap;
  }
  constructor(
    public name: string,
    public tokenValue: string,
    public emitter: string,
    private parentEventPipeList: Array<EventPipe>
  ) {
    this.eventSubject = new Subject<any>();
    this.subscriptionMap = new Map<object, Subscription>();
    this.onceSubscriptionMap = new Map<object, Subscription>();
    if (this.parentEventPipeList) {
      this.parentEventPipeList.push(this);
    }
  }

  /**
   * 
   */
  post(args: any, sender?: any, eventType?: any, eventId?: number) {
    const eventData = {
      args: args,
      sender: sender,
      eventType,
      eventId
    };
    this.eventSubject.next(eventData);
  }

  /**
   * 
   */
  subscribe(eventHandler: (value: any) => void, receiver: object): IDisposable {

    // callercaller
    // 
    const subscriptionInMap = this.subscriptionMap.get(receiver);
    if (subscriptionInMap != null) {
      subscriptionInMap.unsubscribe();
      this.subscriptionMap.delete(receiver);
    }

    const subscription = this.eventSubject.subscribe((eventData: any) => {
      const args = eventData.args;
      const sender = eventData.sender;
      const eventType = eventData.eventType || null;
      const eventId = eventData.eventId || 0;
      if (this.lastEventId >= eventId) {
        return;
      }
      this.lastEventId = eventId;
      // senderreceiverAppContext
      // SPA
      if (!(eventType === EventTypeEnum.ROUTE)) {
        if (this.isInSampeScope(sender, receiver) === false) {
          return;
        }
      }
      eventHandler.call(receiver, args);
    });

    this.subscriptionMap.set(
      receiver,
      subscription
    );
    return this;
  }

  subscribeOnce(eventHandler: (value: any) => void, caller: object): IDisposable {
    const subscription = this.eventSubject.subscribe((value) => eventHandler.call(caller, value));
    this.onceSubscriptionMap.set(
      caller,
      subscription
    );
    return this;
  }

  unSubscribe(subscriber: object) {
    let subscription = this.subscriptionMap.get(subscriber);
    if (subscription) {
      subscription.unsubscribe();
      subscription = null;
      this.subscriptionMap.delete(subscriber);
    } else {
      subscription = this.onceSubscriptionMap.get(subscriber);
      if (subscription) {
        subscription.unsubscribe();
        subscription = null;
        this.onceSubscriptionMap.delete(subscriber);
      }
    }
  }

  // once
  unSubscribeForOnce() {
    for (const subscriber of Array.from(this.onceSubscriptionMap.keys())) {
      this.unSubscribe(subscriber);
    }
  }

  matchEmitterToken(emitter: string, tokenValue: string): boolean {
    if (this.emitter && emitter && this.emitter !== emitter) {
      return false;
    }
    if (this.tokenValue && tokenValue && this.tokenValue !== tokenValue) {
      return false;
    }
    return true;
  }

  examByTargetToken(target: string, tokenValue: string): boolean {
    if (this.emitter !== target) {
      return false;
    }
    if (this.tokenValue !== tokenValue) {
      return false;
    }
    return true;
  }

  dispose(subscriber: object): void {
    this.unSubscribe(subscriber);
    if (this.subscriptionMap.size === 0 && this.parentEventPipeList) {
      const location = this.parentEventPipeList.findIndex(item => item === this);
      if (location !== -1) {
        this.parentEventPipeList.splice(location, 1);
      }
    }
  }

  /**
   * caller
   */
  disposeByCaller(caller: any) {
    const subscriptionInMap = this.subscriptionMap.get(caller);
    if (subscriptionInMap != null) {
      subscriptionInMap.unsubscribe();
      this.subscriptionMap.delete(caller);
    }
  }

  /**
   * 
   * @todo
   * 1senderreceiver
   * 2
   */
  private isInSampeScope(sender: any, receiver: any): boolean {
    // 
    if (this.eventPipeType === EventPipeType.Parsing) {
      return true;
    }

    // sender
    if (!sender) {
      return true;
    }

    // 
    if (sender === receiver) {
      return true;
    }

    // FrameContext
    if (!sender.context || !sender.context.appContext ||
      !receiver.context || !receiver.context.appContext) {
      return false;
    }

    const senderAppContext = sender.context.appContext;
    const receiverAppContext = receiver.context.appContext;

    // 1
    // AppContext
    // root-componentroot-componetnAppContext
    // SPARoot AppContext
    // AppContext
    // AppContext
    if (senderAppContext === receiverAppContext) {
      return true;
    }

    // 2
    // AppContext
    // root-componentAppContext;
    // root-componentAppContextRoot AppContext
    // if (senderAppContext.root === receiverAppContext.root) {
    //   return true;
    // }

    // 3
    // ;
    // 2AppContext
    // Root Appcontext
    // 1senderinjectorinjectorAppContext
    // 2

    if ((senderAppContext.useIsoluteEventBus && senderAppContext.isoluteEventBus) ||
      (receiverAppContext.useIsoluteEventBus && receiverAppContext.isoluteEventBus)) {
      // js  
      return true;
    }
    return false;
  }
}
