/**
 * 
 */
export interface Change {

  /**
   * 
   */
  type: ChangeType;

  /**
   * 
   */
  path: string[];

  /**
   * 
   */
  value?: any;

  /**
   * 
   */
  preValue?: any;

  /**
   * 
   */
  errors?: any;

  /**
   * 
   */
  cb?: Function;

  /**
   * true
   */
  isSubmitted?: boolean;

  /**
   * grididerrorsform
   * {
   *  [id]: errors
   * }
   * 
   */
  id?: string;

  isUdt?: boolean;

  isGrid?: boolean;
  /**
   * 
   */
  create?: boolean;
  /**
   * 
   */
  isCloned?: boolean;
  [prop: string]: any;
}

/**
 * 
 */
export enum ChangeType {
  Update = 'Update',
  Load = 'Load',
  Append = 'Append',
  Remove = 'Remove',
  Swap = 'Swap',
  SelectionChanged = 'SelectionChanged',
  ValueChanged = 'ValueChanged',
  UpdateErrors = 'UpdateErrors',
  GlobalSelectionChanged = 'GlobalSelectionChanged',
  /**
   * 
   */
  PaginationInfoChange = 'PaginationInfoChange',
}

/**
 * 
 */
export interface ViewChange {
  type: ViewChangeType;
  path: string[];
  value: any;
  preValue?: any;
  context?: any;
  [prop: string]: any;
}

/**
 * 
 */
export enum ViewChangeType {
  ValueChanged
}

/**
 * Form
 */
export interface FormValueChange {
  path: string[]; // 
  value: any; // 
  preValue?: any; // 
  command: string; // 
  entityChanged: boolean; // 
}

/**
 * 
 */
export interface EntityValueChange {
  paths: string[]; // 
  value: any; // 
  preValue?: any; // 
  changed: boolean; // 
  [prop: string]: any;
}