import { makePropDecorator } from '../../metadata/index';
import { ClassType } from '../types';
import { ValidateRule } from '../validator/index';

function isPlainObject(value): boolean {
  if (!(typeof value === 'object' && value !== null) || Object.prototype.toString.call({}) !== '[object Object]') {
    return false;
  }
  if (Object.getPrototypeOf(value) === null) {
    return true;
  }
  let proto = value;
  while (Object.getPrototypeOf(proto) !== null) {
    proto = Object.getPrototypeOf(proto);
  }
  return Object.getPrototypeOf(value) === proto;
}

export interface NgProperty {

  /**
   * 
   * @summary
   * key
   */
  dataField?: string;

  /**
   * 
   */
  originalDataField?: string;

  /**
   * 
   */
  originalDataFieldType?: string;

  /**
   * 
   */
  type?: any;

  /**
   * 
   * @description schemepath
   */
  path?: string;

}


/**
 * --------------------------------------------------------------------------------
 * NgField
 * --------------------------------------------------------------------------------
 */

/**  */
export const NG_FIELD = 'NgField';

/**  */
export interface NgFieldProperty extends NgProperty {

  /**  */
  property?: string;

  /**  */
  dataField?: string;

  /**  */
  originalDataField?: string;

  /**  */
  originalDataFieldType?: string;

  /**  */
  primary?: boolean;

  /**  */
  foreign?: boolean;

  /**  */
  defaultValue?: any;

  /**  */
  validRules?: ValidateRule[];

  /**  */
  initValue?: any;

  /**  */
  enableMultiLangInput?: boolean;
  /**
   * 
   */
  enableTimeZone?: boolean;
  /**
   * 
   * @description schemepath
   */
  path?: string;
}

/**  */
export interface NgFieldPropertyDecorator {
  /**
   * @constructor
   * @param obj obj NgFieldPropertystringClassType
   *
   * string 
   *
   * ClassType
   */
  (obj: NgFieldProperty | string | ClassType): any;
  (obj?: NgFieldProperty | string | boolean): any;
  new(obj?: NgFieldProperty | string | boolean): any;
}

function makeNgFieldDecorator(options?: NgFieldProperty | string | boolean): any {
  let metadata: NgFieldProperty = {
    primary: false,
    foreign: false
  };

  if (options) {
    const paramType = typeof options;
    switch (paramType) {
      case 'boolean':
        metadata.primary = Boolean(options);
        break;
      case 'string':
        metadata.dataField = String(options);
        break;
      case 'object':
        metadata = Object.assign(metadata, options);
        break;
    }
  }
  return metadata;
}

/**
 * 
 *
 * ### 
 * ```
 * export class UserEntity extends Entity {
 *      @NgField({
 *          primary: true,
 *          dataField: 'id',
 *          ...
 *      })
 *      userid: string;
 *      userName: string;
 * }
 * ```
 */
export const NgField: NgFieldPropertyDecorator = makePropDecorator(NG_FIELD, makeNgFieldDecorator);


/**
 * --------------------------------------------------------------------------------
 * NgField
 * --------------------------------------------------------------------------------
 */

/** NgList  */
export const NG_LIST = 'NgList';

/**
 * 
 */
export interface NgListProperty extends NgProperty {
  /**  */
  tableName?: string;
  /**  */
  dataField?: string;
  /**  */
  originalDataField?: string;
  /**  */
  type?: any;
  /**
   * 
   * @description schemepath
   */
  path?: string;
}

/**  */
export interface NgListPropertyDecorator {
  /**
   * @constructor
   * @param obj obj NgListPropertystringClassType
   *
   * string 
   *
   * ClassType
   */
  (obj: NgListProperty | string | ClassType): any;
  new(obj: NgListProperty | string | ClassType): any;
}

function makeNgListDecorator(options: NgListProperty | string | ClassType): any {
  if (isPlainObject(options)) {
    return options;
  }

  const type = typeof options;
  if (type === 'string') {
    return {
      dataField: options
    };
  }

  if (type === 'function') {
    return {
      type: options
    };
  }
}

/**
 * 
 * ### 
 * ```
 * export class UserEntity extends Entity {
 *      @NgField({
 *          primary: true,
 *          dataField: 'id',
 *          ...
 *      })
 *      userid: string;
 *      userName: string;
 *      @NgList({
 *          dataField: 'roles',
 *          formType: Role
 *      })
 *      Roles: EntityList<Role>
 * }
 * ```
 */
export const NgList: NgListPropertyDecorator = makePropDecorator(NG_LIST, makeNgListDecorator);


