/**
 * 
 * ### 
 *
 * ```
 *  export class UserEntity entends Entity {
 *      @NgField(true)  // 
 *      id: string;
 *      @NgField('name')  //  name
 *      userName: string;
 *
 *      depid: string;
 *
 *      @NgObject(Department)  //  Department 
 *      epartment: Department;
 *  }
 * ```
 */
export const NgObject: NgObjectPropertyDecorator = makePropDecorator(NG_OBJECT, makeNgObjectDecorator);


