/**
 * 
 */
enum DataPathNodeType {

  /**
   * 
   */
  DataId   = 'DataId',

  /**
   * 
   */
  PropName = 'PropName',
}


/**
 * 
 */
class DataPathNode {

  /**
   * 
   */
  prev: DataPathNode;

  /**
   * 
   */
  next: DataPathNode;

  /**
   * 
   */
  type: DataPathNodeType;

  /**
   * 
   * List  value
   * Objectvalue
   */
  value: any;

  /**
   * 
   */
  constructor(type: DataPathNodeType, data: any) {
    this.type = type;
    this.value = data;

    this.prev = null;
    this.next = null;
  }
}


export { DataPathNodeType, DataPathNode };
