import { CacheKeyCompare, ICacheKeyCompare, ICacheObject, ICacheProvider, Key } from "../types";

export class MemoryCacheProvider<T> implements ICacheProvider<T> {
  protected store: Array<ICacheObject<T>>;
  protected cacheKeyCompare: ICacheKeyCompare;

  constructor(cacheKeyCompare?: ICacheKeyCompare) {
    this.store = new Array<ICacheObject<T>>();
    this.cacheKeyCompare = cacheKeyCompare || new CacheKeyCompare();
  }
  public has(key: Key): boolean {
    if (this.store.length < 1) {
      return false;
    }
    const index = this.store.findIndex((item: ICacheObject<T>) => item && this.cacheKeyCompare.compare(key, item.key));
    return index !== -1;
  }
  public length(): number {
    return this.store.length;
  }
  public set(value: ICacheObject<T>) {
    this.store.push(value);
  }
  public get(key: Key): ICacheObject<T> {
    if (this.store.length < 1) {
      return undefined;
    }
    return this.store.find((item: ICacheObject<T>) => this.cacheKeyCompare.compare(key, item.key));
  }
  public delete(key: Key) {
    if (this.store.length < 1) {
      return;
    }
    const index = this.store.findIndex((item: ICacheObject<T>) => item && this.cacheKeyCompare.compare(key, item.key));
    this.store.splice(index, 1);
  }
  public clear() {
    this.store.length = 0;
  }
  public keys(): IterableIterator<Key> {
    return this.store.keys();
  }
  public values(): IterableIterator<ICacheObject<T>> {
    return this.store.values();
  }
}