import { Injectable, Injector, Inject } from '@angular/core';
// import { FRAME_ID, NAMESPACE } from '../frame/index';
import { UIState, UIStateObservableParam } from '../ui-state/index';
import { ChangeListener } from './change_listener';
import { Expression } from '../expression/index';
import { BindingData } from '../binding-data/index';
import { FRAME_ID, NAMESPACE } from '../frame/tokens';
type EventArgs = Expression.EventArgs;
/**
 * UIState
 */
@Injectable()
class UIStateChangeListener extends ChangeListener {
  constructor(private injector: Injector, private uiState: UIState, @Inject(NAMESPACE) private namespace, @Inject(FRAME_ID) private frameId: string, private bindingData: BindingData) {
    super();
    this.registerEvent();
  }
  public buildEventPath(change: UIStateObservableParam): string[] {
    return null;
  }
  private registerEvent() {
    if (this.uiState && this.uiState.changes) {
      this.uiState.changes.subscribe((change: UIStateObservableParam) => {
        const modification: EventArgs = {
          ns: this.namespace,
          path: [change.field],
          type: Expression.EventType.ValueChanged,
          value: change.value,
          source: Expression.EventSource.State,
          frameId: this.frameId
        };
        // console.log("UIStateChangeListener", modification);
        this.subject.next(modification);
      });
    }
  }
}
export { UIStateChangeListener };
