import { Inject, Injectable, Injector } from '@angular/core';
import { Expression } from '../expression/index';
import { NAMESPACE } from '../frame/tokens';
import { UIState } from '../ui-state/index';

@Injectable()
export class UIStateEffector implements Expression.Effector {
  public ns: string;
  constructor(private injector: Injector, private uiState: UIState, @Inject(NAMESPACE) private namespace) {
    this.ns = namespace;
  }
  public effect(path: string, value: any, options: Expression.EffectOptions) {
    this.uiState.setPropertyValue(path, value);
  }
}