import { Inject, Injectable, Optional } from "@angular/core";
import { Expression } from "../expression/index";
import { EVENT_HANDLER_TOKEN } from "../expression/tokens";
import { RepositoryAddEntityEventHandler } from "./repository_add_entity_event_handler";
import { RepositoryRemoveEntityEventHandler } from "./repository_remove_entity_event_handler";
import { EntityValueChangedEventHandler } from "./entity_value_changed_event_handler";
import { StateValueChangedEventHandler } from "./state_value_changed_event_handler";
import { EntityUpdateEventHandler } from "./entity_update_event_handler";
import { RepositoryLoadEventHandler } from "./repository_load_event_handler";
import { BindingDataAppendObjectEventHandler } from "./binding_data_append_object_event_handler";
import { BindingDataValueChangeEventHandler } from "./binding_data_value_change_event_handler";
import { BindingDataRemoveObjectEventHandler } from "./binding_data_remove_object_event_handler";
import { BindingDataLoadEventHandler } from "./binding_data_load_event_handler";
import { BindingDataSelectionChangedEventHandler } from "./binding_data_selection_changed_event_handler";

@Injectable()
export class EventHandlerRegistry {
  constructor(@Optional() @Inject(EVENT_HANDLER_TOKEN) public handlers: Expression.IEventHandler[]) { }
  /**
   * 
   */
  public get entityValueChangedEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof EntityValueChangedEventHandler);
  }
  /**
   * 
   */
  public get stateValueChangedEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof StateValueChangedEventHandler);
  }
  public get repositoryAddEntityEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof RepositoryAddEntityEventHandler);
  }
  public get repositoryRemoveEntityEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof RepositoryRemoveEntityEventHandler);
  }
  public get entityUpdateEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof EntityUpdateEventHandler);
  }
  public get repositoryLoadEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof RepositoryLoadEventHandler);
  }
  public get bindingDataAppendEntityEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof BindingDataAppendObjectEventHandler);
  }
  public get bindingDataValueChangeEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof BindingDataValueChangeEventHandler);
  }
  public get bindingDataRemoveObjectEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof BindingDataRemoveObjectEventHandler);
  }
  public get bindingDataLoadEventHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof BindingDataLoadEventHandler);
  }
  public get bindingDataSelectionChangedHandler() {
    return this.handlers && this.handlers.find((handler: Expression.IEventHandler) => handler instanceof BindingDataSelectionChangedEventHandler);
  }
}