// tslint:disable: max-line-length

import { List } from 'immutable';
import { Subject } from 'rxjs';

import { Change, ChangeType } from './changes';
import { BindingObject } from './binding_object';
import { BindingProperty } from './binding_property';
import { BindingObjectFactory } from './binding_object_factory';
import { PropertyUtil } from './property_util';
import { BindingData } from './binding_data';
import { TranslateService } from '../i18n/translate_service';
import { IDisposable } from '../core';

/**
 * BindingListBindingObject
 */
class BindingList implements IDisposable {
  public __type__ = 'BindingList';
  /**
   * immutableBindingObject
   */
  private innerList: List<BindingObject>;

  private emptyCurrentItem: BindingObject;

  /**
   * 
   */
  public properties: BindingProperty[];

  /**
   * 
   */
  public primaryKey: string;

  /**
   * 
   */
  public parent: BindingObject;

  /**
   * 
   */
  public changes: Subject<Change>;

  /**
   * 
   */
  public currentId: string;
  /**
   * destory signal
   */
  public destroy$: Subject<any>;

  //#region 

  /**
   * 
   */
  public _paginationInfo: any = null;

  set paginationInfo(sPaginationInfo: any) {
    this._paginationInfo = sPaginationInfo;
    if (this._paginationInfo === sPaginationInfo) {
      return;
    }
    this.changes.next({
      type: ChangeType.PaginationInfoChange,
      path: [],
      value: this._paginationInfo
    });
  }

  get paginationInfo(): any {
    return this._paginationInfo;
  }
  /**
   * 
   */
  get pageIndex() {
    if (!!this.paginationInfo && this.paginationInfo.hasOwnProperty("pageIndex")) {
      return this.paginationInfo.pageIndex;
    }
    return 1;
  }
  /**
   * 
   */
  get pageSize() {
    if (!!this.paginationInfo && this.paginationInfo.hasOwnProperty("pageSize")) {
      return this.paginationInfo.pageSize;
    }
    return 0;
  }

  /**
   * 
   */
  get total() {
    if (!!this.paginationInfo) {
      return this.paginationInfo.total || this.paginationInfo.totalCount;
    }
    return 0;
  }
  /**
   * 
   */
  get skip() {
    const pageIndex = this.pageIndex;
    const pageSize = this.pageSize;
    return (pageIndex - 1) * pageSize;
  }

  /**
   * 
   * @param skip skip
   * @param take take
   */
  public setPaginationInfo(skip: number, take: number) {
    this.paginationInfo = Object.assign({}, this.paginationInfo, {
      pageSize: take,
      pageIndex: skip / take + 1
    });
    /*this.changes.next({
      type: ChangeType.PaginationInfoChange,
      path: [],
      value: this.paginationInfo
    });*/
  }
  //#endregion



  /**
   * 
   * currentIdnull
   */
  public get currentItem(): BindingObject {
    const currentItem = this.findById(this.currentId);
    if (!currentItem) {
      if (!this.emptyCurrentItem) {
        this.emptyCurrentItem = BindingObjectFactory.create(this.properties);
      }
      return this.emptyCurrentItem;
    }
    return currentItem;
  }

  /**
   * 
   */
  public get length(): number {
    return this.innerList.count();
  }

  /**
   * 
   */
  constructor(properties: BindingProperty[]) {
    this.properties = properties;
    this.primaryKey = PropertyUtil.getPrimaryKey(properties);

    this.changes = new Subject<Change>();
    this.innerList = List<BindingObject>();
    this.currentId = null;
    this.destroy$ = new Subject();
  }
  /**
   * 
   * @param options 
   */
  dispose(options?: any) {
    this.clear(true);
    if (this.destroy$) {
      this.destroy$.next();
      this.destroy$.complete();
      this.destroy$ = null;
    }
  }

  /**
   * [Symbol.iterator]for of
   */
  [Symbol.iterator]() {
    const self = this;
    let index = -1;
    const size = this.innerList.size;

    return {
      next: function () {
        index++;
        if (index < size) {
          return { done: false, value: self.innerList.get(index) };
        }
        return { done: true, value: undefined };
      }
    };
  }

  /**
   * 
   * @param objects 
   */
  public load(objects: BindingObject[], entityCreate: boolean = false): void {
    // 
    this.innerList = this.innerList.clear();
    if (objects.length !== 0) {
      // 
      objects.forEach((object: BindingObject) => {
        this.add(object);
      });

      // 
      const currentItem = this.findById(this.currentId);
      if (!currentItem) {
        const firstId = objects[0][this.primaryKey];
        this.setCurrentId(firstId, false, false);
      }
    } else {
      this.currentId = null;
    }
    const change: Change = {
      type: ChangeType.Load,
      path: [],
      value: objects
    };
    change.create = entityCreate;
    // 
    this.changes.next(change);
  }

  /**
   * 
   * @param objects 
   */
  public append(objects: BindingObject[], isCloned: boolean = false): void {
    if (objects.length === 0) {
      return;
    }
    // BindingObject
    objects.forEach((object: BindingObject) => {
      this.add(object);
    });
    // 1
    const lastId = objects[objects.length - 1][this.primaryKey];
    this.setCurrentId(lastId, true, true);
    // 
    const change: Change = {
      type: ChangeType.Append,
      path: [],
      value: objects
    };
    if (isCloned) {
      change.isCloned = true;
    }
    this.changes.next(change);
  }
  /**
   * 
   * @param objects 
   * @description 
   */
  public addData(objects: BindingObject[]): void {
    if (objects.length === 0) {
      return;
    }
    // BindingObject
    objects.forEach((object: BindingObject) => {
      this.add(object);
    });
    // 
    this.changes.next({
      type: ChangeType.Append,
      path: [],
      value: objects
    });
  }
  public insert(object: BindingObject, position?: 1 | -1) {
    const currentIndex = this.innerList.findIndex((obj: BindingObject) => obj.primaryKeyValue === this.currentId);
    // BindingObject
    if (position === 1) {
      this.innerList = this.innerList.insert(currentIndex + 1, object);
    } else if (position === -1) {
      this.innerList = this.innerList.insert(currentIndex, object);
    } else {
      this.innerList = this.innerList.push(object);
    }
    object.parent = this;
    // objectlistpath
    object.changes.subscribe((change: Change) => {
      this.changes.next(change);
    });

    this.setCurrentId(object.primaryKeyValue, true, true);

    // 
    this.changes.next({
      type: ChangeType.Append,
      path: [],
      value: object
    });
  }
  /**
   * 
   * @param object 
   */
  public add(object: BindingObject) {
    this.innerList = this.innerList.push(object);
    object.parent = this;

    // objectlistpath
    object.changes.subscribe((change: Change) => {
      this.changes.next(change);
    });
  }

  /**
   * 
   * @param ids 
   */
  public removeByIds(ids: Array<string>): void {
    if (!ids || ids.length === 0) {
      return;
    }

    let nextCurrentId = this.currentId;
    ids.forEach((id: string) => {

      // 
      if (id === nextCurrentId) {
        nextCurrentId = this.getCurrentIdBeforeDeleting();
      }

      // 
      const index = this.getIndexById(id);
      if (index === -1) {
        return;
      }
      this.innerList = this.innerList.delete(index);
    });

    // 
    if (this.innerList.count() === 0) {
      this.currentId = null;
    } else {
      this.setCurrentId(nextCurrentId, false, false);
    }

    // 
    this.changes.next({
      type: ChangeType.Remove,
      path: [],
      value: ids
    });
  }
  /**
   * 
   * @param ids ids
   */
  public removeDataByIds(ids: Array<string>): void {
    if (!ids || ids.length === 0) {
      return;
    }

    ids.forEach((id: string) => {
      // 
      const index = this.getIndexById(id);
      if (index === -1) {
        return;
      }
      this.innerList = this.innerList.delete(index);
    });

    // 
    this.changes.next({
      type: ChangeType.Remove,
      path: [],
      value: ids
    });
  }
  /**
   * 
   */
  public clear(clearOnly: boolean = false) {
    this.innerList.forEach((item: BindingObject) => {
      item['_ENTITY_'] = null;
      item.unsubscribe.next();
      item.unsubscribe.complete();
      item.changes.complete();
      item.viewChanges.complete();
    });
    this.innerList = this.innerList.clear();
    if (!clearOnly) {
      this.currentId = null;
      this.changes.next({
        type: ChangeType.Remove,
        path: [],
        value: []
      });
    }
  }

  /**
   * 
   * - 11
   * - 1
   */
  public getCurrentIdBeforeDeleting(): string {
    let nextIndex = -1;
    const currentIndex = this.getIndexById(this.currentId);
    if (currentIndex === this.length - 1) {
      nextIndex = currentIndex - 1;
    } else {
      nextIndex = currentIndex + 1;
    }
    return this.getIdByIndex(nextIndex);
  }

  /**
   * 
   * @param   id 
   * @returns BindingObject null
   */
  public findById(id: string): BindingObject | null {
    let target: BindingObject;
    target = this.innerList.find((item) => {
      return item.getValue(this.primaryKey) === id;
    });
    return target === undefined ? null : target;
  }

  /**
   * id
   * @param  id        
   * @param  emitEvent 
   * @param  force 
   */
  public setCurrentId(id: string, emitEvent: boolean = true, emitGlobalEvent: boolean = true, force: boolean = false): void {
    if (this.currentId === id && !force) {
      return;
    }

    // null
    // const currentObj = this.findById(id);
    // if (!currentObj) {
    //   this.currentId = null;
    // } else {
    //   this.currentId = id;
    // }

    // @todonull
    const currentObj = this.findById(id);
    if (!currentObj && !force) {
      return;
    }
    this.currentId = id;

    // 
    if (emitEvent === true) {
      this.changes.next({
        type: ChangeType.SelectionChanged,
        path: [],
        value: this.currentItem,
        force
      });
    }

    // 
    if (emitGlobalEvent === true) {
      this.changes.next({
        type: ChangeType.GlobalSelectionChanged,
        path: [],
        value: this.currentItem,
        force
      });
    }
  }

  /**
   * id
   * @param id 
   * @returns index-1
   */
  public getIndexById(id: string): number {
    return this.innerList.findIndex((obj: BindingObject) => {
      return obj[this.primaryKey] === id;
    });
  }

  /**
   * 
   * @reutrn null
   */
  public getIdByIndex(index: number): string {

    if (index < 0 || index > this.length) {
      return null;
    }

    if (this.innerList.has(index) === false) {
      return null;
    }
    const obj = this.innerList.get(index);
    return obj[this.primaryKey];
  }

  /**
   * BindingObject
   */
  public toArray(): BindingObject[] {
    return this.innerList.toArray();
  }
  /**
   * 
   * @param id1 id1
   * @param id2 id2
   */
  public swapById(id1: any, id2: any) {
    const item1 = this.innerList.find(element => element.primaryKeyValue === id1);
    const item2 = this.innerList.find(element => element.primaryKeyValue === id2);
    this.innerList = this.innerList.map((bindingObject: BindingObject, index: number) => {
      if (bindingObject.primaryKeyValue === id1) {
        return item2;
      } else if (bindingObject.primaryKeyValue === id2) {
        return item1;
      } else return bindingObject;
    }).toList();
    this.changes.next({
      type: ChangeType.Swap,
      path: []
    });
  }
  /**
   * JSON
   * @returns 
   */
  public toJSON(options?: any): any[] {
    const result = [];
    this.innerList.forEach((obj: BindingObject) => {
      result.push(obj.toJSON(options));
    });
    return result;
  }

  /**
   * 
   * @param path 
   * @param defaultValue 
   */
  public getPaginationConfigByPath(path: string, defaultValue?: any) {
    if (!path || path === '/') {
      return this.paginationInfo;
    }
    if (typeof path !== 'string') {
      throw new Error('');
    }
    path = path.substring(1);
    const paths = path.split('/').filter(item => !!item && item.trim().length > 0).map(item => item.trim());
    let config = this.paginationInfo;
    paths.forEach(item => {
      if (config && config.hasOwnProperty(item)) {
        config = config[item];
      } else {
        config = null;
      }
    });
    return !!config ? config : typeof defaultValue !== 'undefined' ? defaultValue : undefined;
  }
  /**
   * bindingList
   * @param string 
   * @param directions 
   * @param options 
   */
  public sortBy(fields: string | Array<string>, directions: string | Array<string>, options?: { [prop: string]: any }) {
    if (!fields || fields.length < 1 || !directions || directions.length < 1) {
      throw new Error('sortBy:argument error');
    }
    // 
    const arrFields: Array<string> = typeof fields === 'string' ? fields.split(',') : fields || [];
    const arrDirections: Array<string> = typeof directions === 'string' ? directions.split(',') : directions || [];
    // 
    if (arrFields.length !== arrDirections.length || arrFields.length < 1) {
      throw new Error('sortBy:fields and directions not match');
    }
    // nage,age,total
    const comparator = (props: Array<string>, orders: Array<string>) => (item1: BindingObject, item2: BindingObject) => {
      return props.reduce((result, prop) => {
        if (result === 0) {
          const bindingProperty = this.properties.find(item => item.name === prop);
          let isMultiLangProp = false;
          if (bindingProperty) {
            isMultiLangProp = bindingProperty.enableMultiLangInput;
          }
          const currentLanguage = TranslateService.getCurrentLanguage();
          const order = ['asc'].includes(orders[props.indexOf(prop)]) ? 1 : -1;
          let item1Value = this.getValue(item1, prop, isMultiLangProp, currentLanguage);
          let item2Value = this.getValue(item2, prop, isMultiLangProp, currentLanguage);

          if (item1Value === null || item1Value === undefined) { item1Value = ''; }
          if (item2Value === null || item2Value === undefined) { item2Value = ''; }

          if (typeof item1Value === 'string' && typeof item2Value === 'string') {
            const localeCompareResult = item1Value.localeCompare(item2Value);
            result = localeCompareResult * order;
          } else {
            if (item1Value > item2Value) { result = order * 1; }
            if (item1Value < item2Value) { result = order * -1; }
          }
        }
        return result;
      }, 0);
    };
    this.innerList = this.innerList.sort(comparator(arrFields, arrDirections)).toList();
  }
  private getValue(target: any, propName: string, isMultiLangProp: boolean = false, currentLanguage: string = 'zh-CHS') {
    if (target instanceof BindingList) {
      target = target.currentItem;
    } else if (target instanceof BindingData) {
      target = target.list.currentItem;
    }
    let result = null;
    if (propName.indexOf('.') === -1) {
      result = target[propName];
    } else {
      const props = propName.split('.');
      for (const prop of props) {
        target = result = this.getValue(target, prop, isMultiLangProp, currentLanguage);
      }
    }
    if (isMultiLangProp && result && result.hasOwnProperty(currentLanguage)) {
      return result[currentLanguage];
    } else {
      return result;
    }
  }
}

export { BindingList };
