import { ICacheObject, Key, TTL } from "../types";

export class CacheObject<T> implements ICacheObject<T>{
  public createAt: Date;
  constructor(public key: Key, public content: T, public ttl: TTL) {
    this.createAt = new Date();
  }
  public isExpired(): boolean {
    if (typeof this.ttl === 'number') {
      return Date.now().valueOf() > this.createAt.valueOf() + this.ttl;
    } else {
      return Date.now() > this.ttl.valueOf();
    }
  }
}