import { Injectable } from '@angular/core';

@Injectable()
class Context {

  /**
   * 
   */
  params: Map<string, any> = new Map<string, any>();

  /**
   * 
   */
  getParam(key: string): any {
    return this.params.get(key);
  }

  /**
   * 
   */
  setParam(key: string, value: any) {
    this.params.set(key, value);
  }
}

export { Context };
