import { StateMachine } from './state_machine';
import { State } from './types';
import { Context } from '../context/context';
import { VariableParseService } from '../variable/index';
import { FrameContext } from '../frame/index';
import { StateMachineEvent } from './state_machine_event';

/**
 * 
 */
export class StateMachineContext {

  /**
   * 
   */
  state: string;

  parent: Context;

  parser: VariableParseService;

  frameContext: FrameContext;

  stateMachineEvent: StateMachineEvent;
  /**
   * 
   * @param stateMachine 
   * @param initialState 
   */
  constructor(public stateMachine: StateMachine) {
    // this.state = initialState.name;
  }

  initialize(variableParseService: VariableParseService, initialState: State) {
    this.frameContext = this.stateMachine && this.stateMachine.frameContext || null;
    this.state = this.state || (initialState ? initialState.name : '');
    this.parser = variableParseService;
    this.stateMachineEvent = this.stateMachine.stateMachineEvent;
  }

  /**
   * 
   * @param stateName 
   */
  transitTo(stateName: string) {
    const nextState = this.stateMachine.states[stateName];
    if (nextState) {
      this.state = nextState.name;
      this.stateMachine.render();
    }
  }

  parse(expression: string, targetType: 'source' | 'target'): any {
    if (expression === null || expression === undefined) {
      return expression;
    }
    const context = this.stateMachineEvent.getFrameContext(expression) || this.stateMachine.frameContext;
    switch (targetType) {
      case 'source':
        return this.parseSourceValue(expression, context);
      case 'target':
        return this.parser.parse(expression, context);
    }
  }

  private parseSourceValue(expression: string, context: any): any {
    if (expression === null || expression === undefined) {
      return expression;
    }
    let result = expression.trim();
    result = this.parser.parse(result, context);
    if (result === 'state') {
      result = this.state;
    }
    return result;
  }

  // 
  get(expression: string): any {
    return this.getUIState(expression);
  }
  // uistate
  getUIState(expression: string): any {
    if (!expression) {
      return;
    }
    const frameContext = this.stateMachineEvent.getFrameContext(expression);
    if (!frameContext) {
      return;
    }
    this.stateMachineEvent.ListenUIStateChange(frameContext, expression);
    if (this.parser) {
      const value = this.parser.parse(expression, frameContext);
      if (value === null) {
        return null;
      }
      if (typeof value === 'object' && Object.keys(value).length === 0) {
        return null;
      }
      return value;
    } else {
      throw new Error('');
    }
  }

  // data
  getData(expression: string): any {
    if (!expression) {
      return;
    }
    const frameContext = this.stateMachineEvent.getFrameContext(expression);
    if (!frameContext) {
      return;
    }
    this.stateMachineEvent.ListenEntityChange(frameContext, expression);
    if (this.parser) {
      const value = this.parser.parse(expression, frameContext);
      if (value === null) {
        return null;
      }
      if (typeof value === 'object' && Object.keys(value).length === 0) {
        return null;
      }
      return value;
    } else {
      throw new Error('');
    }
  }
}
