import { Injectable, Injector } from "@angular/core";
import { ExpressionManager } from "./expression_manager";;
import { ExpressionRegistry } from "./expression_registry";
import { ExpressionResult } from "./expression_result"
import { Expression } from "./types";

@Injectable()
export class ExpressionResultFactory {
  constructor(private injector: Injector, private expressionRegistry: ExpressionRegistry, private expressionManager: ExpressionManager, private expressionResult: ExpressionResult) {
    this.registeEvent();
  }
  private registeEvent() {
    this.expressionRegistry.expressions.subscribe((expressionObjects: Expression.ExpressionObject[]) => {
      // 
      expressionObjects.forEach((expressionObject: Expression.ExpressionObject) => {
        if (expressionObject.deps && expressionObject.deps.length > 0) {
          return;
        }
        const result = this.expressionManager.eval(expressionObject.id);
        this.expressionResult[expressionObject.id] = result;
      });
    });
  }
}