/**
 * 
 */
export class FilterConditionManager {
    private filters: Map<string, Array<any>> = new Map<string, Array<any>>();
    /**
     * 
     * @param bindingPath 
     */
    public getFilters(bindingPath: string) {
        return this.filters.get(bindingPath) || [];
    }
    /**
     * 
     * @param bindingPath 
     * @param merge 
     */
    public mergeCondition(bindingPath: string, merge: (conditions: any) => any) {
        const conditions = this.filters.get(bindingPath) || [];
        const result = merge(conditions);
        this.filters.set(bindingPath, result);
    }
    /**
     * 
     * @param bindingPath 
     * @param condition 
     */
    public addCondition(bindingPath: string, condition: any) {
        // bindingPath
        const conditions = this.filters.get(bindingPath);
        const index = this.findConditionIndex(bindingPath, condition);
        if (index !== -1) {
            conditions[index] = condition;
        } else {
            conditions.push(condition);
        }
    }
    /**
     * 
     * @param bindingPath 
     * @param conditions 
     */
    public addConditions(bindingPath: string, conditions: Array<any>) {
        if (!conditions || !Array.isArray(conditions) || conditions.length < 1) {
            return;
        }
        conditions.forEach(condition => {
            this.addCondition(bindingPath, condition);
        });
    }
    /**
     * 
     * @param bindingPath 
     * @param predicate 
     */
    public removeCondition(bindingPath: string, predicate: (value: any, index: number, array: any[]) => boolean) {
        const conditions: Array<{ [propName: string]: any }> = this.filters.get(bindingPath);
        if (!conditions || conditions.length < 1) {
            return;
        }
        const removes = conditions.filter(predicate);
        if (removes) {
            removes.forEach(condition => {
                const index = this.findConditionIndex(bindingPath, condition);
                if (index >= 0) {
                    conditions.splice(index, 1);
                }
            });
        }
    }
    /**
     * 
     */
    public clear() {
        this.filters.clear();
    }
    /**
     * 
     * @param bindingPath 
     * @param conditions 
     */
    public setConditions(bindingPath: string, conditions: Array<any>) {
        this.filters.set(bindingPath, conditions);
    }

    /**
     * 
     * @param bindingPath 
     * @param condition 
     */
    private findConditionIndex(bindingPath: string, condition: any) {
        if (!condition || typeof (condition) !== 'object' || Object.keys(condition).length < 1) {
            return -1;
        }
        const conditions: Array<any> = this.filters.get(bindingPath);
        if (!conditions || conditions.length < 1) {
            return -1;
        }
        return conditions.findIndex((value, index) => {
            let isEqual = true;
            const props = Object.keys(condition);
            for (const prop of props) {
                if (!(value && value.hasOwnProperty(prop) && value[prop] === condition[prop])) {
                    isEqual = false;
                    break;
                }
            }
            return isEqual;
        });
    }
}
