import { MetadataUtil } from '../metadata/index';
import { NG_COMPONENT_STATE, NgParam } from './decorators';

export class UIStateMetadataUtil {

  /**
   * NgUIState
   * @param 
   * @returns 
   * @example
   * 
   * {
   *    '': <NgUIStateProperty>{ ...}
   * }
   */
  static getUIFields(target: Function): {[propName: string]: NgParam} {
      return MetadataUtil.getPropsMetadatasByName(target, NG_COMPONENT_STATE);
  }
}