
/**
 * 
 */
class FormPathConverter {

  /**
   * ControlPathString => BingingPathArray
   * @params controlPath FormControlBindingDatabindingPaht + FormControlbinding
   * @return BindingPath
   */
  public static toBindingPathArray(formPahtString: string): string[] {
    const bindingPathArray = formPahtString.split('.').filter((part: string) => {
      return part !== '';
    });
    return bindingPathArray;
  }
}

export { FormPathConverter };
