import { Injectable } from '@angular/core';
import { VariableParser } from './variable_parser';
const EVENT_PARAMS_EXP = /\{EVENTPARAM~\/(\S+?)\}/g;
const EVENT_PARAM_EXP = /\{EVENTPARAM~\/(\S+?)\}/;

/**
 * 
 */
@Injectable()
export class EventParamVariableParser implements VariableParser {
  public context: any;

  public parse(expression: string, context: any, eventParam?: any) {
    this.context = context;
    const variables = this.extractVariables(expression);
    if (!variables || variables.length < 1) {
      return expression;
    }
    variables.forEach(variableExpression => {
      const exprs = variableExpression.match(EVENT_PARAM_EXP);
      if (exprs && exprs.length === 2) {
        const variable = exprs[1];
        const value = this.getVariableValue(variable, eventParam);
        expression = expression.replace(EVENT_PARAM_EXP, value);
      }
    });
    return expression;
  }
  /**
   * 
   * @param expression 
   * @returns 
   */
  private extractVariables(expression: string) {
    if (!expression) {
      return null;
    }
    const variables = expression.match(EVENT_PARAMS_EXP);
    if (!variables || variables.length < 1) {
      return null;
    }
    return variables;
  }
  /**
   * 
   * @param variable 
   */
  private getVariableValue(variable: string, eventParam: any) {
    if (!eventParam || !variable) {
      return null;
    }
    const variables = variable.split('/').filter(p => p);
    const result = variables.reduce((target: any, key: string) => {
      if (target) {
        return target[key];
      } else {
        return null;
      }
    }, eventParam);
    return result;
  }
}