import { IContextMetadata } from './app_metadata';

/**
 * ContextMetadata
 */
class ContextMetadataManager {

  /**
   * ContextMetadataMap
   */
  private contextMetadataMap: Map<string, IContextMetadata>;

  /**
   * 
   */
  constructor() {
    this.contextMetadataMap = new Map<string, IContextMetadata>();
  }

  /**
   * ContextMetadataMap
   */
  public getContextMetadataMap(): Map<string, IContextMetadata> {
    return this.contextMetadataMap;
  }

  /**
   * nameContextMetadata
   * @return undefined
   */
  public getContextMetadataByName(name: string): IContextMetadata {
    return this.contextMetadataMap.get(name);
  }

  /**
   * ContextMetadata
   */
  public register(name: string, contextMetadata: IContextMetadata): void {
    this.contextMetadataMap.set(name, contextMetadata);
  }

  /**
   * ContextMetadata
   */
  public exsit(name: string) {
    const contextMetadata = this.getContextMetadataByName(name);
    return contextMetadata ? true : false;
  }

}

export { ContextMetadataManager };
