import { BindingData } from '../binding-data';
import { IDisposable } from '../core';

/**
 * BindingData
 */
class BindingDataManager implements IDisposable {

  /**
   * BindingDataMap
   */
  private bindingDataMap: Map<string, BindingData>;

  /**
   * 
   */
  constructor() {
    this.bindingDataMap = new Map<string, BindingData>();
  }

  /**
   * BindingDataMap
   */
  public getBindingDataMap(): Map<string, BindingData> {
    return this.bindingDataMap;
  }

  /**
   * nameBindingData
   * @return undefined
   */
  public getBindingDataByName(name: string): BindingData {
    return this.bindingDataMap.get(name);
  }

  /**
   * BindingData
   * @description bindingData
   */
  public regBindingData(name: string, bindingData: BindingData): void {
    this.bindingDataMap.set(name, bindingData);
  }
  public unRegisteBindingData(name: string) {
    this.bindingDataMap.delete(name);
  }
  /**
   * Repository
   */
  public ifBindingDataExits(name: string) {
    const bindingData = this.getBindingDataByName(name);
    return bindingData ? true : false;
  }
  public dispose() {
    this.bindingDataMap.clear();
  }

}

export { BindingDataManager };
