import { Subject, of } from 'rxjs';
import { Map } from 'immutable';
import { Change, ChangeType, ViewChange, ViewChangeType } from './changes';
import { BindingProperty, BindingPropertyType } from './binding_property';
import { BindingList } from './binding_list';
import { InvokeOnValueChange } from './types';

/**
 * 
 */
abstract class BaseBindingObject {
  public __type__ = 'BindingObject';
  // protected data: any = undefined;
  /**
   * immutable
   */
  protected innerValues: Map<string, any>;
  /**
   * 
   */
  protected currentLanguage: string;
  /**
   * 
   */
  public parent: BindingList | BaseBindingObject;

  /**
   * 
   */
  public changes: Subject<Change>;

  /**
   * 
   */
  public viewChanges: Subject<ViewChange>;

  /**
   *  
   */
  public properties: BindingProperty[];

  /**
   * 
   */
  public primaryKey: string;

  /**
   * 
   */
  public get primaryKeyValue() {
    return this.primaryKey ? this.getValue(this.primaryKey) : '';
  }

  /**
   * 
   */
  public isShowValidationMsg = false;
  /**
   * 
   */
  public unsubscribe: Subject<any> = new Subject();
  /**
   * { [propertyName]: FormControl }control
   */
  public controlMap: any = {};
  constructor() {
    this.innerValues = Map<string, any>();
    this.changes = new Subject<Change>();
    this.viewChanges = new Subject<ViewChange>();
  }
  /**
   * 
   */
  public setShowValidationMsg(flag: boolean) {
    this.isShowValidationMsg = flag;
  }
  // public abstract load(data: any);
  /**
   * 
   * @param   propertyName 
   * @returns 
   */
  public getValue(propertyName: string): any {
    return this.innerValues.get(propertyName);
  }

  /**
   * 
   * @param propertyName        
   * @param propertyValue       
   * @param emitEventToView     Viewfalse
   * @param emitEventToEntity   Entityfalse
   * @param errors              
   * @param invokeOnValueChange 
   */
  public setValue(propertyName: string, propertyValue: any, emitEventToView: boolean = false, emitEventToEntity: boolean = false, errors?: any, invokeOnValueChange?: InvokeOnValueChange, context?: any): void {
    // 
    // if (this.primaryKey && !this.primaryKeyValue && this.primaryKey !== propertyName) {
    //   return;
    // }
    const oldPropertyValue = this.getValue(propertyName);
    // @oldPropertyValue === propertyValue
    // if (oldPropertyValue === propertyValue) {
    //   return;
    // }
    if (!invokeOnValueChange || oldPropertyValue === propertyValue) {
      // 
      invokeOnValueChange = function (preValue, value, entityChanged: boolean, primaryValue?: any) {
        return of(true);
      };
    }

    if (emitEventToEntity === true) {
      // BUG 322301@2019.08.10; ; Load
      // if(!this.innerValues.has(propertyName)) {
      //   return;
      // }
      // 
      invokeOnValueChange(oldPropertyValue, propertyValue, false, this.primaryKeyValue).subscribe((result) => {
        if (result) {
          // 
          this.innerValues = this.innerValues.set(propertyName, propertyValue);
          // this.data[propertyName] = propertyValue;
          const viewChange = this.buildViewChangesContext(propertyName, propertyValue, oldPropertyValue, errors, context);
          this.viewChanges.next(viewChange);
          // 
          if (emitEventToView === true) {
            const change = this.buildChangesContext(propertyName, propertyValue, context, errors);
            this.changes.next(change);
          }
          // 
          invokeOnValueChange(oldPropertyValue, propertyValue, true, this.primaryKeyValue).subscribe();
        } else {
          // 
          // 
          const change = this.buildChangesContext(propertyName, oldPropertyValue, context, errors);
          this.changes.next(change);
        }
      });
    } else {
      // `emitEventToEntity === false`, 
      this.innerValues = this.innerValues.set(propertyName, propertyValue);
      // this.data[propertyName] = propertyValue;
      if (emitEventToView === true) {
        const change = this.buildChangesContext(propertyName, propertyValue, context, errors);
        this.changes.next(change);
      }
      // 
      invokeOnValueChange(oldPropertyValue, propertyValue, true, this.primaryKeyValue).subscribe();
    }
  }
  /**
   * BindingObjectJSON
   */
  public toJSON(options?: any): any {
    const langCode = this.getCurrentLanguage();//window.localStorage.getItem('languageCode') || 'zh-CHS';
    const result = {};
    this.properties.forEach((property: BindingProperty) => {
      const propName = property.name;
      if (property.type === BindingPropertyType.List) {
        const list: BindingList = this[propName];
        result[propName] = list.toJSON(options);
      } else if (property.type === BindingPropertyType.Object) {
        const object: BaseBindingObject = this[propName];
        result[propName] = object.toJSON(options);
      } else if (property.type === BindingPropertyType.Dynamic) {
        const object: BaseBindingObject = this[propName];
        result[propName] = object.toJSON(options);
      } else {
        // 1
        // 2ignoreMultiLangInput
        if (property.enableMultiLangInput === true) {
          // 
          if (options && options.ignoreMultiLangInput === true) {
            const multiLangValueObj = this.getValue(propName);
            if (multiLangValueObj) {
              result[propName] = multiLangValueObj[langCode];
            } else {
              result[propName] = multiLangValueObj;
            }
          } else if (options && options.useFullMultiLangProperty) {
            //  @2021-10-13
            const multiLangValueObj = this.getValue(propName);
            if (multiLangValueObj) {
              result[`${propName}_MULTILANGUAGE`] = multiLangValueObj;
              // 
              result[propName] = multiLangValueObj[langCode];
            }
          } else {
            const multiLangValueObj = this.getValue(propName);
            if (!multiLangValueObj) {
              result[propName] = { [langCode]: multiLangValueObj };
            } else {
              result[propName] = multiLangValueObj;
            }
          }
        } else {
          result[propName] = this.getValue(propName);
        }
      }
    });

    return result;
  }
  /**
   * 
   * @returns 
   */
  protected getCurrentLanguage() {
    this.currentLanguage = this.currentLanguage || window.localStorage.getItem('languageCode') || 'zh-CHS';
    return this.currentLanguage;
  }
  /**
   * bindignData
   * @param propertyName 
   * @param propertyValue 
   * @param context 
   * @param errors 
   * @param type 
   * @returns 
   */
  private buildChangesContext(propertyName: string, propertyValue: any, context?: any, errors?: any, type: ChangeType = ChangeType.ValueChanged): Change {
    return {
      type: type,
      path: [propertyName],
      value: propertyValue,
      id: this.primaryKeyValue,
      errors: errors,
      context
    };
  }
  /**
   * viewChanges
   * @param propertyName 
   * @param value 
   * @param preValue 
   * @param errors 
   * @param context 
   * @param type 
   * @returns 
   */
  private buildViewChangesContext(propertyName: string, value: any, preValue: any, errors?: any, context?: any, type: ViewChangeType = ViewChangeType.ValueChanged): ViewChange {
    return {
      type: type,
      path: [propertyName],
      value: value,
      preValue: preValue,
      errors: errors,
      context
    };
  }
}
export { BaseBindingObject }