import { Translate } from './translate';

export class TranslateService {
  private static defaultLanguage = 'zh-CHS';
  public static translate: Translate = null;
  /**
   * 
   * @param translate translate
   */
  public static setTranslate(translate: Translate) {
    if (translate) {
      this.translate = translate;
    }
  }
  /**
   * 
   */
  public static getCurrentLanguage() {
    return this.translate && this.translate.getCurrentLanguage() || this.defaultLanguage;
  }
  public static dispose() {
    this.translate = null;
  }
}
